<template>
  <!-- <div id="echarts" :style="{width: '600px', height: '400px'}"></div> -->
  <!-- <div :id="echartsId" :style="{height:'100%'}"></div> -->
  <div :id="echartsId" :style="{height:'100%', width:'100%'}"></div>
</template>
<script>
export default {
  name: "echartsUtil",
  data() {
    return {
      myChart: null, //图表对象
      optionData: null //配置对象
      // chartHeight:document.getElementById(this.echartsId).height(),
    };
  },
  props: {
    //echarts的id,必须有
    echartsId: {
      type: String,
      required: true,
      default: "echarts"
    },
    //主标题文本，支持使用 \n 换行。
    title: {
      type: String,
      required: false,
      default: null
    },
    //是否显示标题
    titleShow: {
      type: Boolean,
      default: false
    },
    //提示框组件是否显示。
    tooltipShow: {
      type: Boolean,
      default: true
    },
    //提示框组件触发方式
    tooltipTrigger: {
      type: String,
      default: "axis"
    },
    //图例的数据数组。
    legendData: {
      type: Array,
      required: false
    },
    //是否使用坐标轴配置
    useAxis: {
      type: Boolean,
      default: false
    },
    //x轴坐标轴名称。
    xAxisName: {
      type: String,
      required: false
    },
    //x轴坐标轴类型。
    xAxisType: {
      type: String,
      default: "category"
    },
    //x轴类目数据，在类目轴（type: 'category'）中有效。
    xAxisData: {
      type: Array,
      required: false
    },
    //y轴坐标轴类型。
    yAxisType: {
      type: String,
      default: "value"
    },
    //y轴坐标轴名称。
    yAxisName: {
      type: String,
      required: false
    },
    //y轴的位置。
    yAxisPosition: {
      type: String,
      default: "left"
    },
    //y轴名称位置
    yAxisNameLocation: {
      type: String,
      default: "end"
    },
    //刻度标签的内容格式器，支持字符串模板和回调函数两种形式。
    yAxisFormatter: {
      type: Function,
      required: false
    },
    //每个系列通过 type 决定自己的图表类型
    seriesType: {
      type: String,
      default: "line",
      required: false
    },
    //折线图标题设置
    titleParam: {
      type: Object,
      default: null
    },
    //颜色配置
    colorParam: {
      type: Array,
      default: null
    },
    //提示框组件配置
    tooltipParam: {
      type: Object,
      default: null
    },
    //坐标轴指示器对象数据
    axisPointerParam: {
      type: Object,
      default: null
    },
    //图例组件
    legendParam: {
      type: Object,
      default: null
    },
    //图例是否显示
    legendShow: {
      type: Boolean,
      default: true
    },
    //图例的配置
    gridParam: {
      type: Object,
      default: null
    },
    //直角坐标系 grid 中的 x 轴
    xAxisParam: {
      type: Object,
      default: null
    },
    //直角坐标系 grid 中的 y 轴
    yAxisParam: {
      type: Array,
      default: null
    },
    //系列列表。每个系列通过 type 决定自己的图表类型
    seriesParam: {
      type: Array,
      default: () => []
    },
    //是否显示 symbol(标记的图形),
    seriesSymbolShow: {
      type: Boolean,
      default: true
    },
    //系列名称，用于tooltip的显示，legend 的图例筛选，在 setOption 更新数据和配置项时用于指定对应的系列。
    seriesName: {
      type: String,
      default: null
    },
    //组件 ID。默认不指定。
    seriesId: {
      type: String,
      default: null
    },
    //系列中的数据内容数组。数组项通常为具体的数据项。
    seriesData: {
      type: Array,
      default: () => []
    },
    //图形上的文本标签，可用于说明图形的一些数据信息
    seriesLabelShow: {
      type: Boolean,
      default: false
    },
    //饼图展示成南丁格尔图
    seriesRoseType: {
      type: String,
      default: null
    },
    //饼图半径大小
    seriesRadius: {
      type: Array,
      default: null
    },
    //标题距离y轴距离
    yNameGap: {
      type: Number,
      default: 13
    },
    //图例组件离容器上侧的距离。
    legendBottom: {
      type: Number,
      default: 0
    },
    //图例标记的图形宽度。
    legendItemWidth: {
      type: Number,
      default: 15
    },
    //图例标记的图形高度。
    legendItemHeight: {
      type: Number,
      default: 7
    },
    //grid 组件离容器左侧的距离
    gridLeft: {
      type: Number,
      default: 20
    },
    axisLabelFormatter:{
      type:Function,
      required:false
    },
    seriesConnectNulls:{
      type:Boolean,
      default:true
    }
  },
  created: function() {
    this.addWatchToProps();
  },
  mounted: function() {
    this.initEchart(this.echartsId);
    //解决id和数据同时传递过来的时候,图形初始化绘制时图形没有初始化完毕数据无法渲染问题
    this.$nextTick(() => {
      this.optionOpt();
    });
  },
  watch: {
    // echartsId: {
    //   deep: true,
    //   handler(val, oldval) {
    //     this.optionOpt();
    //   }
    // }
  },
  computed: {
    titleValue() {
      if (this.titleParam != null) {
        return this.titleParam;
      } else {
        let title = {
          //是否显示标题组件。
          show: this.titleShow,
          //主标题文本，支持使用 \n 换行。
          text: this.title
        };
        return title;
      }
    },
    colorValue() {
      if (this.colorParam != null) {
        return this.colorParam;
      } else {
        let color = [
          //"#ff8315",
          "#00c0ff",
          "#ffae68",
          "#f8c516",
          "#ff1dc1",
          "#4000ff",
          "#01a63d",
          "#de4343",
          "#0273ff",
          "#9933cc",
          "#9d2d1f",
          "#f8c209",
          "#00bbc7",
          "#7249ee",
          "#005d84",
          "#6ca200",
          "#ff7bdb"
        ];
        return color;
      }
    },
    tooltipValue() {
      if (this.tooltipParam != null) {
        return this.tooltipParam;
      } else {
        let tooltip = {
          show: this.tooltipShow,
          //触发类型。
          trigger: this.tooltipTrigger,
          //提示框浮层的背景颜色。
          backgroundColor: "rgba(0,0,0,0.7)",
          //提示框浮层内边距，单位px，默认各方向内边距为5，接受数组分别设定上右下左边距。
          padding: 10,
          //提示框浮层的文本样式。
          textStyle: {
            color: "#ffffff",
            fontSize: 12
          },
          //坐标轴指示器配置项。
          axisPointer: {
            type: "line",
            z: 1,
            lineStyle: {
              color: "#ffe329"
            }
          }
        };
        return tooltip;
      }
    },
    axisPointerValue() {
      if (this.axisPointerParam != null) {
        return this.axisPointerParam;
      } else {
        let axisPointer = {
          label: {
            color: "#ffffff",
            backgroundColor: "rgba(0,0,0,.7)"
          }
        };
        return axisPointer;
      }
    },
    legendValue() {
      if (this.legendParam != null) {
        return this.legendParam;
      } else {
        let legend = {
          data: this.legendData, //图例的数据数组。数组项通常为一个字符串，每一项代表一个系列的 name（如果是饼图，也可以是饼图单个数据的 name）
          //图例的类型。可选值：'plain'：普通图例。缺省就是普通图例。'scroll'：可滚动翻页的图例。当图例数量较多时可以使用。
          type: "scroll",
          //图例组件是否显示
          show: this.legendShow,
          //图例组件离容器下侧的距离。
          bottom: this.legendBottom,
          //图例项的 icon。
          // ECharts 提供的标记类型包括 'circle', 'rect', 'roundRect', 'triangle', 'diamond', 'pin', 'arrow', 'none'
          // icon: "roundRect",
          //图例标记的图形宽度。
          itemWidth: this.legendItemWidth,
          //图例标记的图形高度。
          itemHeight: this.legendItemHeight,
          //图例关闭时的颜色。
          inactiveColor: "#ccc",
          //图例的公用文本样式。
          textStyle: {
            //颜色
            color: "#888888",
            //字体大小
            fontSize: 12,
            //行高。
            lineHeight: 1
          }
          //
        };
        return legend;
      }
    },
    gridValue() {
      if (this.gridParam != null) {
        return this.gridParam;
      } else {
        let grid = {
          //grid 组件离容器上侧的距离。
          top: 10,
          //grid 组件离容器下侧的距离。
          bottom: 40,
          //grid 组件离容器左侧的距离。
          //left 的值可以是像 20 这样的具体像素值，可以是像 '20%' 这样相对于容器高宽的百分比，也可以是 'left', 'center', 'right'。
          //如果 left 的值为'left', 'center', 'right'，组件会根据相应的位置自动对齐。
          left: this.gridLeft,
          //grid 组件离容器右侧的距离。
          right: 10,
          //grid 区域是否包含坐标轴的刻度标签。
          containLabel: true
        };
        return grid;
      }
    },
    xAxisValue() {
      if (this.xAxisParam != null) {
        return this.xAxisParam;
      } else {
        let xAxis = {
          //坐标轴名称。
          name: this.xAxisName,
          //坐标轴名称显示位置。
          nameLocation: "end",
          //坐标轴名称与轴线之间的距离。
          nameGap: 0,
          //坐标轴名称的文字样式。
          nameTextStyle: {
            color: "#4e4e4e",
            fontSize: 12
          },
          //坐标轴类型。
          // 可选：'value' 数值轴，适用于连续数据。
          // 'category' 类目轴，适用于离散的类目数据，为该类型时必须通过 data 设置类目数据。
          // 'time' 时间轴，适用于连续的时序数据，与数值轴相比时间轴带有时间的格式化，在刻度计算上也有所不同，例如会根据跨度的范围来决定使用月，星期，日还是小时范围的刻度。
          // 'log' 对数轴。适用于对数数据。
          type: this.xAxisType,
          //坐标轴刻度相关设置。
          axisTick: {
            //类目轴中在 boundaryGap 为 true 的时候有效，可以保证刻度线和标签对齐。
            alignWithLabel: true,
            //刻度线的样式设置。
            lineStyle: {
              color: "#eeeeee"
            }
          },
          //坐标轴轴线相关设置。
          axisLine: {
            //是否显示坐标轴轴线。
            show: true,
            //坐标轴线线的相关配置
            lineStyle: {
              type: "solid",
              color: "#d0d0d0"
            }
          },
          //坐标轴刻度标签的相关设置。
          axisLabel: {
            color: "#888888",
            fontSize: 11,
            align: "center",
            formatter:this.axisLabelFormatter
          },
          //坐标轴在 grid 区域中的分隔线。
          splitLine: {
            //网格线
            show: true,
            lineStyle: {
              type: "dotted",
              color: "#d8d8d8"
            }
          },
          //类目数据，在类目轴（type: 'category'）中有效。
          // 如果没有设置 type，但是设置了 axis.data，则认为 type 是 'category'。
          data: this.xAxisData
        };
        return xAxis;
      }
    },
    yAxisValue() {
      if (this.yAxisParam != null) {
        return this.yAxisParam;
      } else {
        let yAxis = [
          //y轴配置
          {
            //坐标轴类型
            type: this.yAxisType,
            //坐标轴名称。
            name: this.yAxisName,
            //坐标轴名称与轴线之间的距离。
            nameGap: this.yNameGap,
            //坐标轴名称显示位置。
            nameLocation: this.yAxisNameLocation,
            //y 轴的位置。
            position: this.yAxisPosition,
            //坐标轴轴线相关设置。
            axisLine: {
              //是否显示坐标轴轴线。
              show: true,
              //坐标轴线线的配置
              lineStyle: {
                color: "#d0d0d0"
              }
            },
            //坐标轴刻度标签的相关设置。
            axisLabel: {
              //刻度标签的内容格式器，支持字符串模板和回调函数两种形式。
              formatter: this.yAxisFormatter,
              color: "#888888",
              fontSize: 11,
              margin: 10
            },
            //坐标轴刻度相关设置。
            axisTick: {
              //刻度线的样式设置。
              lineStyle: {
                color: "#eeeeee"
              }
            },
            //坐标轴在 grid 区域中的分隔线。
            splitLine: {
              //网格线
              show: true,
              lineStyle: {
                type: "dotted",
                color: "#d8d8d8"
              }
            },
            //坐标轴名称的文字样式。
            nameTextStyle: {
              color: "#888888",
              fontSize: 12,
              align: "left"
            }
          }
        ];
        return yAxis;
      }
    },
    seriesValue() {
      let data = this.seriesParam;
      if (data.length > 0) {
        return data;
      } else {
        let series = [
          {
            //图形类型
            type: this.seriesType,
            id: this.seriesId,
            name: this.seriesName,
            data: this.seriesData,
            symbol: "emptyCircle",
            symbolSize: 6,
            showSymbol: this.seriesSymbolShow,
            cursor: "pointer",
            color: this.colorParam,
            radius: this.seriesRadius,
            roseType: this.seriesRoseType,
            labelLine: {
              normal: {
                length: 5,
                length2: 5
              }
            },
            label: {
              normal: {
                formatter: "{d}% {c}"
              },
              show: this.seriesLabelShow
            },
            connectNulls:this.seriesConnectNulls
          }
        ];
        return series;
      }
    }
  },
  methods: {
    initEchart(value) {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.getElementById(value));
      //全局设置Echrts - 根据窗口的大小变动图表
      var _this = this;
      window.addEventListener("resize", function() {
        _this.myChart.resize();
      });
    },
    optionOpt() {
      if (this.myChart) {
        // 指定图表的配置项和数据
        this.optionData = {
          //标题组件，包含主标题和副标题。
          title: this.titleValue,
          //调色盘颜色列表。如果系列没有设置颜色，则会依次循环从该列表中取颜色作为系列颜色。
          color: this.colorValue,
          //提示框组件。
          tooltip: this.tooltipValue,
          //这是坐标轴指示器（axisPointer）的全局公用设置。
          axisPointer: this.axisPointerValue,
          //图例组件。图例组件展现了不同系列的标记(symbol)，颜色和名字。
          //可以通过点击图例控制哪些系列不显示。
          legend: this.legendValue,
          //图例的配置
          grid: this.gridValue,
          //直角坐标系 grid 中的 x 轴，一般情况下单个 grid 组件最多只能放上下两个 x 轴，
          //多于两个 x 轴需要通过配置 offset 属性防止同个位置多个 x 轴的重叠。
          // xAxis: this.xAxisValue,
          // yAxis: this.yAxisValue,
          //系列列表。每个系列通过 type 决定自己的图表类型
          series: this.seriesValue
        };

        //是否使用坐标轴设置,饼图等不需要坐标轴的可以忽略此处
        if (this.useAxis) {
          this.optionData["xAxis"] = this.xAxisValue;
          this.optionData["yAxis"] = this.yAxisValue;
        }
        this.optionInit();
      }
    },
    optionInit() {
      // 使用刚指定的配置项和数据显示图表。
      this.myChart.resize();
      this.myChart.setOption(this.optionData);
    },
    addWatchToProps() {
      var _this3 = this;

      var watchedVariable = this._watchers.map(function(watcher) {
        return watcher.expression;
      });
      Object.keys(this.$props).forEach(function(prop) {
        if (!~watchedVariable.indexOf(prop)) {
          var opts = {};
          if (
            ~["[object Object]", "[object Array]"].indexOf(
              _this3.getType(_this3.$props[prop])
            )
          ) {
            opts.deep = true;
          }
          _this3.$watch(
            prop,
            function() {
              _this3.optionOpt();
            },
            opts
          );
        }
      });
    },
    getType(v) {
      return Object.prototype.toString.call(v);
    },
    resizeOption() {
      this.myChart.resize();
    }
  }
};
</script>


