// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
// 引入element UI
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
// 引入axios
//import axios from 'axios'
import { fetchGet, fetchPost} from '@/utils/http' 
// 引入QS
import qs from 'qs'
// 引入jquery
import jquery from 'jquery'
//引入echarts
import echarts from 'echarts'

import Vue from 'vue'
import App from './App'
//import home_page from './components/home/home_page'
import router from './router'

import Tools from '@/utils/tools' 

Vue.use(ElementUI);

// 挂载到vue原型链上
//Vue.prototype.$http = axios
//使用axios例子参见文件上传
Vue.prototype.$get = fetchGet
Vue.prototype.$post = fetchPost
//
Vue.prototype.$qs = qs

Vue.prototype.$casLoginUrl = process.env.casLoginUrl
Vue.prototype.$casLogoutUrl = process.env.casLogoutUrl
Vue.prototype.$baseUrl = process.env.baseUrl
Vue.prototype.$webUrl = process.env.webUrl
Vue.prototype.$modifyPwdUrl = process.env.modifyPwdUrl
Vue.prototype.$tools = Tools

//注册到全局
window.echarts=echarts
//
window.jquery=jquery

Vue.config.productionTip = false
//axios.defaults.withCredentials = true
/* eslint-disable no-new */
new Vue({
  el: '#app',
  //render: h => h(App),
  router,
  components: { App },
  template: '<App/>'
})



