import Vue from 'vue'
import Router from 'vue-router'
import homepage from '@/components/home/home_page'
import Dashboard from '@/components/Dashboard/Dashboard'
import fileManage from '@/components/fileManage/fileManage'
import ZKInfo from '@/components/ZKInfo/ZKInfo'
import systemMonitor from '@/components/systemMonitor/systemMonitor'

Vue.use(Router)

const routers = new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'homepage',
    //   component: homepage
    // },
    {
      // path: '/Dashboard',
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/fileManage',
      name: 'fileManage',
      component: fileManage
    },
    {
      path: '/ZKInfo',
      name: 'ZKInfo',
      component: ZKInfo
    },
    {
      path: '/systemMonitor',
      name: 'systemMonitor',
      component: systemMonitor
    },
  ]
})

export default routers;


