<template>
  <div class="mainBox">
    <div class="el-row fileManage systemMonitor">
      <div class="el-col el-col-24 page_content_top">
        <div class="left pull-left">
          <el-select v-model="selectNode" @change="selectChange">
            <el-option
              v-for="item in nodeListInfo"
              :key="item.id"
              :label="item.host"
              :value="item.id"
            ></el-option>
          </el-select>
        </div>
        <div class="right pull-right">
          <el-tooltip class="item" content="刷新" placement="bottom">
            <span class="refresh">
              <span class="icon-refresh" @click="refreshOperate()"></span>
            </span>
          </el-tooltip>
        </div>
      </div>
      <div class="el-col el-col-24 page_content_cont">
        <el-card class="box-card card-top">
          <div class="CPU-coreNum">
            <h4 class="title-cont">{{cpuCores}}</h4>
            <span class="title">CPU核数</span>
          </div>
          <div class="CPU-brand">
            <h4 class="title-cont">{{cpuBrand}}</h4>
            <span class="title">CPU品牌</span>
          </div>
          <div class="memorySize">
            <h4 class="title-cont">
              {{memoryTotalBytes}}
              <span class="gb">GB</span>
            </h4>
            <span class="title">内存大小</span>
          </div>
          <div class="operatingSystem">
            <h4 class="title-cont">{{osDesc}}</h4>
            <span class="title">操作系统版本号</span>
          </div>
        </el-card>
        <div class="card-box">
          <el-row :gutter="20">
            <!-- CPU使用率 -->
            <el-col :span="12">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="cardTitle">CPU使用率</span>
                </div>
                <div class="chart-box">
                  <div class="chart-cont">
                    <v-echarts
                      ref="cpuChart"
                      :echartsId="cupChart"
                      :seriesParam="cpuSeriesParam"
                      :yAxisName="cpuYName"
                      :xAxisType="xType"
                      :useAxis="true"
                      :yAxisNameLocation="yAxisNameLocation"
                      :axisLabelFormatter="axisLabelFormatter"
                      :gridLeft="gridLeft"
                      :yNameGap="90"
                    ></v-echarts>
                  </div>
                </div>
              </el-card>
            </el-col>
            <!-- 内存/SWAP -->
            <el-col :span="12">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="cardTitle">内存/SWAP</span>
                </div>
                <div class="chart-box">
                  <div class="chart-cont">
                    <v-echarts
                      ref="memChart"
                      :echartsId="memChart"
                      :seriesParam="memSeriesParam"
                      :yAxisName="memYName"
                      :xAxisType="xType"
                      :useAxis="true"
                      :yAxisNameLocation="yAxisNameLocation"
                      :axisLabelFormatter="axisLabelFormatter"
                      :gridLeft="gridLeft"
                      :yNameGap="35"
                    ></v-echarts>
                  </div>
                </div>
              </el-card>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <!-- 磁盘使用率 -->
            <el-col :span="12">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="cardTitle">磁盘使用率</span>
                </div>
                <div class="chart-box">
                  <el-select v-model="selectDiskusage" @change="diskusageInit()">
                    <el-option
                      v-for="item in diskusageName"
                      :key="item"
                      :label="item"
                      :value="item"
                    ></el-option>
                  </el-select>
                  <div class="chart-cont">
                    <v-echarts
                      ref="diskChart"
                      :echartsId="diskChart"
                      :yAxisName="diskYName"
                      :xAxisType="xType"
                      :useAxis="true"
                      :yAxisNameLocation="yAxisNameLocation"
                      :axisLabelFormatter="axisLabelFormatter"
                      :seriesName="diskSeriesName"
                      :seriesData="diskSeriesParam"
                      :gridLeft="gridLeft"
                      :yNameGap="35"
                    ></v-echarts>
                  </div>
                </div>
              </el-card>
            </el-col>
            <!-- 磁盘I/O使用率 -->
            <el-col :span="12">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="cardTitle">磁盘I/O使用率</span>
                </div>
                <div class="chart-box">
                  <el-select v-model="selectDiskio" @change="diskIOInit()">
                    <el-option v-for="item in diskioName" :key="item" :label="item" :value="item"></el-option>
                  </el-select>
                  <div class="chart-cont">
                    <v-echarts
                      ref="diskIOChart"
                      :echartsId="diskIOChart"
                      :yAxisName="diskIOYName"
                      :xAxisType="xType"
                      :useAxis="true"
                      :yAxisNameLocation="yAxisNameLocation"
                      :axisLabelFormatter="axisLabelFormatter"
                      :seriesName="diskIOSeriesName"
                      :seriesData="diskIOSeriesData"
                      :gridLeft="gridLeft"
                      :yNameGap="40"
                    ></v-echarts>
                  </div>
                </div>
              </el-card>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <!-- 网络吞吐量 -->
            <el-col :span="12">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="cardTitle">网络吞吐量</span>
                </div>
                <div class="chart-box">
                  <el-select v-model="selectNet" @change="netInit()">
                    <el-option v-for="item in netName" :key="item" :label="item" :value="item"></el-option>
                  </el-select>
                  <div class="chart-cont">
                    <v-echarts
                      ref="netChart"
                      :echartsId="netChart"
                      :yAxisName="netYName"
                      :xAxisType="xType"
                      :useAxis="true"
                      :yAxisNameLocation="yAxisNameLocation"
                      :axisLabelFormatter="axisLabelFormatter"
                      :seriesParam="netSeriesParam"
                      :gridLeft="gridLeft"
                      :yNameGap="75"
                    ></v-echarts>
                  </div>
                </div>
              </el-card>
            </el-col>
            <!-- Load Average 系统平均负载 -->
            <el-col :span="12">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span class="cardTitle">Load Average</span>
                </div>
                <div class="chart-box">
                  <div class="chart-cont loadChart">
                    <v-echarts
                      ref="loadAverageChart"
                      :echartsId="loadAverageChart"
                      :seriesName="loadAverageSeriesName"
                      :seriesData="loadAverageSeriesData"
                      :yAxisName="loadAverageYName"
                      :xAxisType="xType"
                      :useAxis="true"
                      :yAxisNameLocation="yAxisNameLocation"
                      :axisLabelFormatter="axisLabelFormatter"
                      :gridLeft="gridLeft"
                      :yNameGap="35"
                    ></v-echarts>
                  </div>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Echarts from "../../utils/echartsUtil";
export default {
  name: "systemMonitor",
  components: {
    "v-echarts": Echarts
  },
  data() {
    return {
      selectNode: null, //被选中的节点
      nodeListInfo: [], //节点集合
      cpuCores: null, //cpu核心数
      cpuBrand: null, //cpu品牌
      memoryTotalBytes: null, //内存大小
      osDesc: null, //操作系统版本号
      cupChart: "cupChart", //cup折线图id
      cpuYName: "CPU使用率(%)", //cupY轴名称
      cpuSeriesParam: [], //cpu数据
      yAxisNameLocation: "center", //y轴名称位置
      memChart: "memChart", //内存折线图id
      memSeriesParam: [], //内存折线图数据
      memYName: "内存/SWAP使用量(MB)", //内存Y轴名称
      diskusageList: null, //磁盘集合
      selectDiskusage: null, //选中的磁盘
      diskusageName: [], //磁盘名称集合
      diskChart: "diskChart", //磁盘折线图id
      diskSeriesParam: [], //磁盘数据
      diskYName: "磁盘空间占用率(%)", //磁盘y轴名称
      diskSeriesName: "磁盘空间占用率(%)",
      diskioList: null, //磁盘IO对象
      diskioName: [], //磁盘IO名称结合
      selectDiskio: null, //选中的磁盘IO
      diskIOChart: "diskIOChart", //磁盘IO折线图id
      diskIOYName: "磁盘I/O使用率(%)", //磁盘IOy轴名称
      diskIOSeriesName: "磁盘I/O使用率(%)", //
      diskIOSeriesData: [], //磁盘IO数据
      netList: null, //网络吞吐量对象
      netName: [], //网络吞吐量名称集合
      selectNet: null, //选中的网络吞吐量
      netChart: "netChart", //网络吞吐量折线图id
      netYName: "吞吐量(kb/s)",
      netSeriesParam: [],
      loadAverageChart: "loadAverageChart", //平均负载折线图id
      loadAverageSeriesName: "Load Average",
      loadAverageSeriesData: [],
      loadAverageYName: "Load Average",
      gridLeft: 30,
      yNameGap: 0, //标题距离y轴距离
      xType: "time",
    };
  },
  mounted() {
    this.nodeList();
  },
  methods: {
    //获取所有的节点数据
    nodeList() {
      this.$get("sys/nodes", null)
        .then(res => {
          this.nodeListInfo = res;
          this.selectNode = res[0].id;
          this.nodeInfoInit();
        })
        .catch(() => {});
    },
    //节点数据初始化
    nodeInfoInit() {
      var list = this.nodeListInfo;
      for (var i in list) {
        if (this.selectNode == list[i].id) {
          this.cpuCores = list[i].cpuCores;
          this.cpuBrand = list[i].cpuBrand;
          this.memoryTotalBytes = (
            list[i].totalMemSize /
            (1024 * 1024 * 1024)
          ).toFixed(2);
          this.osDesc = list[i].os;
        }
      }
      this.getCpuData();
      this.getMemData();
      this.getDiskusageData();
      this.getDiskioData();
      this.getNetData();
      this.getLoad();
    },
    //节点选择数据改变
    selectChange() {
      this.initData();
      this.nodeInfoInit();
    },
    //获取CPU数据
    getCpuData() {
      var selectNodeId = this.selectNode;
      var param = {
        minutes: 60
      };
      this.$get("sys/cpu/" + selectNodeId, param)
        .then(res => {
          var datas = res;
          let totalData = [];
          let systemData = [];
          let userData = [];
          let stealData = [];
          let iowaitData = [];
          for (var i in datas) {
            // var time = this.$tools.getTime(datas[i].time);
            totalData.push([datas[i].time,(Number(datas[i].total)*100).toFixed(3)]);
            systemData.push([datas[i].time,(Number(datas[i].system)*100).toFixed(3)]);
            userData.push([datas[i].time,(Number(datas[i].user)*100).toFixed(3)]);
            stealData.push([datas[i].time,(Number(datas[i].steal)*100).toFixed(3)]);
            iowaitData.push([datas[i].time,(Number(datas[i].iowait)*100).toFixed(3)]);
          }
          this.cpuSeriesParam = [
            {
              //图形类型
              type: "line",
              name: "total(%)",
              data: totalData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            },
            {
              //图形类型
              type: "line",
              name: "system(%)",
              data: systemData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            },
            {
              //图形类型
              type: "line",
              name: "user(%)",
              data: userData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            },
            {
              //图形类型
              type: "line",
              name: "steal(%)",
              data: stealData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            },
            {
              //图形类型
              type: "line",
              name: "iowait(%)",
              data: iowaitData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            }
          ];
        })
        .catch(() => {});
    },
    //获取内存/SWAP数据
    getMemData() {
      var selectNodeId = this.selectNode;
      var param = {
        minutes: 60
      };
      this.$get("sys/mem/" + selectNodeId, param)
        .then(res => {
          var datas = res;
          let totalUsedData = [];
          let swapUsedData = [];
          for (var i in datas) {
            // var time = this.$tools.getTime(datas[i].time);
            totalUsedData.push([datas[i].time,
              (datas[i].totalUsed / (1024 * 1024 * 1024)).toFixed(2)
            ]);
            swapUsedData.push([datas[i].time,
              (datas[i].swapUsed / (1024 * 1024 * 1024)).toFixed(2)
            ]);
          }
          this.memSeriesParam = [
            {
              //图形类型
              type: "line",
              name: "内存使用量(MB)",
              data: totalUsedData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            },
            {
              //图形类型
              type: "line",
              name: "swap使用量(MB)",
              data: swapUsedData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer"
            }
          ];
        })
        .catch(() => {});
    },
    //获取磁盘使用率情况
    getDiskusageData() {
      var selectNodeId = this.selectNode;
      var param = {
        minutes: 60
      };
      this.$get("sys/diskusage/" + selectNodeId, param)
        .then(res => {
          this.diskusageList = res;
          this.diskusageName = [];
          for (var item in res) {
            this.diskusageName.push(item);
          }
          this.selectDiskusage = this.diskusageName[0];
          this.diskusageInit();
        })
        .catch(() => {});
    },
    //磁盘数据初始化
    diskusageInit() {
      var datas = this.diskusageList;
      for (var item in datas) {
        if (item == this.selectDiskusage) {
          var data = datas[item];
          this.diskSeriesParam = [];
          for (var i in data) {
            // var time = this.$tools.getTime(data[i].time);
            this.diskSeriesParam.push([data[i].time,Number(data[i].usage)*100]);
          }
        }
      }
    },
    //获取磁盘I/O数据
    getDiskioData() {
      var selectNodeId = this.selectNode;
      var param = {
        minutes: 60
      };
      this.$get("sys/diskio/" + selectNodeId, param)
        .then(res => {
          this.diskioList = res;
          this.diskioName = [];
          for (var item in res) {
            this.diskioName.push(item);
          }
          this.selectDiskio = this.diskioName[0];
          this.diskIOInit();
        })
        .catch(() => {});
    },
    //磁盘I/O数据初始化
    diskIOInit() {
      var datas = this.diskioList;
      for (var item in datas) {
        if (item == this.selectDiskio) {
          var data = datas[item];
          this.diskIOSeriesData = [];
          for (var i in data) {
            // var time = this.$tools.getTime(data[i].time);
            this.diskIOSeriesData.push([data[i].time,(Number(data[i].usage)*100).toFixed(2)]);
          }
        }
      }
    },
    //获取网络接口流量信息
    getNetData() {
      var selectNodeId = this.selectNode;
      // var selectNodeId="123";
      var param = {
        minutes: 60
      };
      this.$get("sys/net/" + selectNodeId, param)
        .then(res => {
          this.netList = res;
          this.netName = [];
          for (var item in res) {
            this.netName.push(item);
          }
          this.selectNet = this.netName[0];
          this.netInit();
        })
        .catch(() => {});
    },
    //网络接口流量数据初始化
    netInit() {
      var datas = this.netList;
      if(Object.keys(datas).length!=0){
        for (var item in datas) {
          if (item == this.selectNet) {
            var data = datas[item];
            let txBytesPsData = [];
            let rxBytesPsData = [];
            for (var i in data) {
              // var time = this.$tools.getTime(data[i].time);
              txBytesPsData.push([data[i].time,(data[i].txBytesPs / 1024).toFixed(2)]);
              rxBytesPsData.push([data[i].time,(data[i].rxBytesPs / 1024).toFixed(2)]);
            }
            this.netSeriesParam = [
              {
                //图形类型
                type: "line",
                name: "TX bytes(kb/s)",
                data: txBytesPsData,
                symbol: "emptyCircle",
                symbolSize: 6,
                showSymbol: true,
                cursor: "pointer"
              },
              {
                //图形类型
                type: "line",
                name: "RX bytes(kb/s)",
                data: rxBytesPsData,
                symbol: "emptyCircle",
                symbolSize: 6,
                showSymbol: true,
                cursor: "pointer"
              }
            ];
          }
        }
      }else{
        this.netSeriesParam = [
              {
                //图形类型
                type: "line",
                name: "TX bytes(kb/s)",
                data: [],
                symbol: "emptyCircle",
                symbolSize: 6,
                showSymbol: true,
                cursor: "pointer"
              },
              {
                //图形类型
                type: "line",
                name: "RX bytes(kb/s)",
                data: [],
                symbol: "emptyCircle",
                symbolSize: 6,
                showSymbol: true,
                cursor: "pointer"
              }
            ];
      }
    },
    //获取系统平均负载信息
    getLoad() {
      var selectNodeId = this.selectNode;
      // var selectNodeId="123";
      var param = {
        minutes: 60
      };
      this.$get("sys/load/" + selectNodeId, param)
        .then(res => {
          this.loadAverageSeriesData = [];
          for (var i in res) {
            // var time = this.$tools.getTime(res[i].time);
            this.loadAverageSeriesData.push([res[i].time,res[i].load]);
          }
        })
        .catch(() => {});
    },
    //全局刷新操作
    refreshOperate() {
      this.nodeInfoInit();
    },
    initData(){
      this.cpuCores=null; //cpu核心数
      this.cpuBrand=null;//cpu品牌
      this.memoryTotalBytes=null; //内存大小
      this.osDesc=null; //操作系统版本号
      this.cpuSeriesParam=[];
      this.memSeriesParam=[];
      this.diskusageName = [];
      this.diskusageList=null;
      this.diskSeriesParam=[];
      this.diskioList=null;
      this.diskIOSeriesData=[]; 
      this.netList=null;
      this.netSeriesParam=[];
      this.loadAverageSeriesData=[];
    },
    axisLabelFormatter: function(value, index) {
      var date = new Date(value);
      var texts = [date.getHours(), date.getMinutes()];
      return texts.join(":");
    }
  }
};
</script>


