<template>
  <!-- 多级级菜单展示 -->
  <div class="common-all-nav" v-if="flagTag">
    <!-- 一级菜单展示 -->
    <div class="common-first-content common-level1-nav">
      <div class="level-inner">
        <ul>
          <template v-for="(item ,index) in menuData">
            <li :id="menu(index)">
              <span class="inner-text" @click="switchMainMenu(item)">{{item.menuname}}</span>
              <i :class="[(item.sublevel).length>0?'icon-arrow-big':'']"></i>
            </li>
          </template>
        </ul>
      </div>
    </div>
    <!-- 一级菜单 end -->

    <!-- 菜单级别为三级，一级菜单‘基础档案’下的二三级菜单 -->
    <template v-for="(item ,index) in thirdData">
      <div :id="thirdmenu(item)" class="leftNav-more">
        <div class="common-last-content common-level3-nav">
          <!-- <div class="nav-search-top">
            <div class="search-box">
              <input type="text" class="form-control" placeholder="搜索" />
              <span class="icon-search"></span>
            </div>
          </div>-->
          <div class="common-last-menu">
            <div class="level-inner">
              <div class="level-inner-column">
                <template v-for="(it ,index) in item.sublevel">
                  <div class="menu-column">
                    <div class="menu-level2">
                      <span class="inner-text">{{it.menuname}}</span>
                    </div>
                    <ul>
                      <template v-for="(itchild ,index) in it.sublevel">
                        <li>
                          <router-link :to="itchild.menuUrl">{{itchild.menuname}}</router-link>
                          <!-- <a href="itchild.menuUrl">
                            <span class="inner-text">{{itchild.menuname}}</span>
                          </a>-->
                        </li>
                      </template>
                    </ul>
                  </div>
                </template>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--‘基础档案’下的二三级菜单 end-->
    </template>

    <!-- 菜单级别为二级，一级菜单‘权限管理’下的二级菜单 -->
    <template v-for="(item ,index) in secondData">
      <div :id="secondmenu(item)" class="leftNav-more">
        <div class="common-last-content common-level3-nav">
          <!-- <div class="nav-search-top">
            <div class="search-box">
              <input type="text" class="form-control" placeholder="搜索" />
              <span class="icon-search"></span>
            </div>
          </div>-->
          <div class="common-last-menu">
            <div class="level-inner">
              <div class="level-inner-column">
                <div class="menu-column">
                  <ul>
                    <template v-for="(it ,index) in item.sublevel">
                      <li>
                        <router-link :to="it.menuUrl">{{it.menuname}}</router-link>
                        <!-- <a href="it.menuUrl">
                          <span class="inner-text">{{it.menuname}}</span>
                        </a>-->
                      </li>
                    </template>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--‘合同管理’下的二级菜单 end-->
    </template>

    <!-- 菜单级别为四级，第一个一级菜单‘模拟四级菜单’下的二三四级菜单 -->
    <template v-for="(item ,index) in forthData">
      <div :id="forthmenu(item)" class="leftNav-more">
        <!-- 菜单级别为四级，第一个一级菜单‘模拟四级菜单’下的二级菜单 -->
        <div class="common-second-content common-level2-nav">
          <div class="level-inner">
            <ul>
              <template v-for="(it ,index) in item.sublevel">
                <li :id="secondId(index)">
                  <span class="inner-text">{{it.menuname}}</span>
                  <i class="icon-arrow-big"></i>
                </li>
              </template>
            </ul>
          </div>
        </div>
        <!-- 菜单级别为四级，‘模拟四级菜单’第一个二级菜单下的三四级菜单 -->
        <template v-for="(it ,index) in item.sublevel">
          <div class="common-last-content common-level4-nav" id="leftNav-second1">
            <!-- <div class="nav-search-top">
              <div class="search-box">
                <input type="text" class="form-control" placeholder="搜索" />
                <span class="icon-search"></span>
              </div>
            </div>-->
            <div class="common-last-menu">
              <div class="level-inner">
                <div class="level-inner-column">
                  <template v-for="(i ,index) in it.sublevel">
                    <div class="menu-column">
                      <div class="menu-level2">
                        <span class="inner-text">{{i.menuname}}</span>
                      </div>
                      <ul>
                        <template v-for="(t ,index) in i.sublevel">
                          <li>
                            <router-link :to="t.menuUrl">{{t.menuname}}</router-link>
                            <!-- <a href="t.menuUrl">
                              <span class="inner-text">{{t.menuname}}</span>
                            </a>-->
                          </li>
                        </template>
                      </ul>
                    </div>
                  </template>
                </div>
              </div>
            </div>
          </div>
        </template>
      </div>
      <!--四级菜单 end -->
    </template>
  </div>
  <!-- 多级菜单展示 end -->
</template>
<script>
export default {
  name: "menushow",
  data() {
    return {
      // menuData: [],
      secondData: [],
      thirdData: [],
      forthData: [],
      flagTag: true,
      menuData: [
        {
          levelnum: 1,
          depth: 1,
          menuname: "首页",
          menuUrl: "/",
          sublevel: [],
          menuid: 100
        },
        {
          levelnum: 1,
          depth: 3,
          menuname: "基础档案",
          menuUrl: null,
          sublevel: [
            {
              levelnum: 2,
              depth: null,
              menuname: "客户管理",
              menuUrl: null,
              sublevel: [
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "客户信息",
                  menuUrl: "/",
                  menuid: 211
                },
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "客户名称信息",
                  menuUrl: "/",
                  menuid: 212
                }
              ],
              menuid: 210
            },
            {
              levelnum: 2,
              depth: null,
              menuname: "产品管理",
              menuUrl: null,
              sublevel: [
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "产品信息",
                  menuUrl: "/",
                  menuid: 221
                },
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "产品著作权",
                  menuUrl: "/",
                  menuid: 222
                }
              ],
              menuid: 220
            }
          ],
          menuid: 200
        },
        {
          levelnum: 1,
          depth: 3,
          menuname: "合同管理",
          menuUrl: null,
          sublevel: [
            {
              levelnum: 2,
              depth: null,
              menuname: "合同列表",
              menuUrl: null,
              sublevel: [
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "合同列表",
                  menuUrl: "/",
                  menuid: 310
                }
              ],
              menuid: 311
            }
          ],
          menuid: 300
        },
        {
          levelnum: 1,
          depth: 1,
          menuname: "上传扫描件",
          menuUrl: "/",
          sublevel: [],
          menuid: 400
        },
        {
          levelnum: 1,
          depth: 3,
          menuname: "财务管理",
          menuUrl: null,
          sublevel: [
            {
              levelnum: 2,
              depth: null,
              menuname: "报告期",
              menuUrl: null,
              sublevel: [
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "报告期",
                  menuUrl: "/",
                  menuid: 541
                }
              ],
              menuid: 540
            },
            {
              levelnum: 2,
              depth: null,
              menuname: "权责发生制",
              menuUrl: null,
              sublevel: [
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "生成权责发生",
                  menuUrl: "/",
                  menuid: 511
                },
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "确认权责发生",
                  menuUrl: "/",
                  menuid: 512
                },
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "生成期末数据",
                  menuUrl: "/",
                  menuid: 513
                }
              ],
              menuid: 510
            },
            {
              levelnum: 2,
              depth: null,
              menuname: "收付实现制",
              menuUrl: null,
              sublevel: [
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "快照数据生成",
                  menuUrl: "/",
                  menuid: 521
                },
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "应收大表",
                  menuUrl: "/",
                  menuid: 522
                }
              ],
              menuid: 520
            },
            {
              levelnum: 2,
              depth: null,
              menuname: "统计查询",
              menuUrl: null,
              sublevel: [
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "月收入任务",
                  menuUrl: "/",
                  menuid: 531
                },
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "财务状况统计",
                  menuUrl: "/",
                  menuid: 532
                },
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "应收统计",
                  menuUrl: "/",
                  menuid: 533
                },
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "权责统计",
                  menuUrl: "/",
                  menuid: 534
                }
              ],
              menuid: 530
            }
          ],
          menuid: 500
        },
        {
          levelnum: 1,
          depth: 3,
          menuname: "权限管理",
          menuUrl: null,
          sublevel: [
            {
              levelnum: 2,
              depth: null,
              menuname: "权限管理",
              menuUrl: null,
              sublevel: [
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "角色管理",
                  menuUrl: "/",
                  menuid: 611
                },
                {
                  levelnum: 3,
                  depth: null,
                  menuname: "部门管理",
                  menuUrl: "/",
                  menuid: 612
                }
              ],
              menuid: 610
            }
          ],
          menuid: 600
        }
      ]
    };
  },
  created: function() {},
  mounted: function() {
    this.showMenuList();
  },
  methods: {
    showMenuList() {
      // var str = sessionStorage.getItem("menuData");
      var datas = this.menuData;
      if (datas == null || datas == "" || datas.length == 0) {
        this.$router.push({ path: "/" });
        return false;
      }
      for (var i = 0; i < datas.length; i++) {
        if (datas[i].depth == 2) {
          //只有2级
          datas[i].depth = i + 1;
          this.secondData.push(datas[i]);
        } else if (datas[i].depth == 3) {
          //只有3级
          datas[i].depth = i + 1;
          this.thirdData.push(datas[i]);
        } else if (datas[i].depth == 4) {
          //四级
          datas[i].depth = i + 1;
          this.forthData.push(datas[i]);
        }
      }
      this.$nextTick(() => {
        this.setMenuShow();
      });
      // this.$post("/common/menuMessage", null)
      //   .then(res => {
      //     var datas = res.datas;
      //     this.menuData = datas;
      //     sessionStorage.removeItem('menuData');
      //     sessionStorage.setItem('menuData',this.menuData);
      //     for (var i = 0; i < datas.length; i++) {
      //       if (datas[i].depth == 2) {
      //         //只有2级
      //         datas[i].depth = i + 1;
      //         this.secondData.push(datas[i]);
      //       } else if (datas[i].depth == 3) {
      //         //只有3级
      //         datas[i].depth = i + 1;
      //         this.thirdData.push(datas[i]);
      //       } else if (datas[i].depth == 4) {
      //         //四级
      //         datas[i].depth = i + 1;
      //         this.forthData.push(datas[i]);
      //       }
      //     }
      //     this.$nextTick(() => {
      //       this.setMenuShow();
      //     });
      //   })
      //   .catch(() => {});
    },
    menu(index) {
      return "menu" + (index + 1);
    },
    thirdmenu(item) {
      return "leftNav-menu" + item.depth;
    },
    secondmenu(item) {
      return "leftNav-menu" + item.depth;
    },
    forthmenu(item) {
      return "leftNav-menu" + item.depth;
    },
    secondId(item) {
      return "second" + (item + 1);
    },
    setMenuShow() {
      // 一级菜单显示 图标
      $(".menu_floatShow")
        .on("mouseover", function() {
          $(this).addClass("active");
          $(".common-all-nav").addClass("active");
          $(".common-first-content").addClass("active");
        })
        .on("mouseout", function() {
          $(this).removeClass("active");
          $(".common-all-nav").removeClass("active");
          $(".common-first-content").removeClass("active");
        });
      //all
      $(".common-all-nav")
        .on("mouseover", function() {
          $(this).addClass("active");
          $(".menu_floatShow").addClass("active");
          $(".common-first-content").addClass("active");
        })
        .on("mouseout", function() {
          $(".menu_floatShow").removeClass("active");
          $(this).removeClass("active");
          $(".common-first-content").removeClass("active");
        });

      var key_value;
      var second_value;
      var obj, second;
      if ($(".common-first-content").length > 0) {
        //鼠标滑过一级菜单 二级竖向菜单
        $(".common-first-content ul li")
          .on("mouseover", function() {
            key_value = $(this).attr("id");
            obj = "#leftNav-" + key_value;
            $(this).addClass("active");
            // $('.common-first-content').addClass('active')
            if ($(obj).find(".common-second-content").length > 0) {
              $(obj)
                .find(".common-second-content")
                .addClass("active");
            } else {
              $(obj)
                .find(".common-second-content")
                .removeClass("active");
              $(obj)
                .find(".common-last-content")
                .addClass("active");
            }
          })
          .on("mouseout", function() {
            // if(!$('.common-first-content').hasClass('active')){
            $(obj)
              .find(".common-second-content")
              .removeClass("active");
            $(obj)
              .find(".common-last-content")
              .removeClass("active");
            $(this).removeClass("active");
            // }
          });
        if ($(".common-second-content").length > 0) {
          $(".common-second-content ul li")
            .on("mouseover", function() {
              second_value = $(this).attr("id");
              second = "#leftNav-" + second_value;
              $(second).addClass("active");
              $(this).addClass("active");
              $("#" + key_value).addClass("active");
            })
            .on("mouseout", function() {
              second_value = "";
              $(second).removeClass("active");
              $(this).removeClass("active");
              $("#" + key_value).removeClass("active");
            });

          $(".common-second-content")
            .on("mouseover", function() {
              $(obj)
                .find(".common-second-content")
                .addClass("active");
              $("#" + key_value).addClass("active");
            })
            .on("mouseout", function() {
              $(obj)
                .find(".common-second-content")
                .removeClass("active");
              $("#" + key_value).removeClass("active");
            });
        }
        if ($(".common-last-content").length > 0) {
          $(".common-last-content")
            .on("mouseover", function() {
              $(obj)
                .find(".common-last-content")
                .addClass("active");
              $(obj)
                .find(".common-second-content")
                .addClass("active");
              $("#leftNav-" + second_value).addClass("active");
              $("#" + key_value).addClass("active");
              $("#" + second_value).addClass("active");
            })
            .on("mouseout", function() {
              $(obj)
                .find(".common-last-content")
                .removeClass("active");
              $(obj)
                .find(".common-second-content")
                .removeClass("active");
              $("#leftNav-" + second_value).removeClass("active");
              $("#" + key_value).removeClass("active");
              $("#" + second_value).removeClass("active");
            });
        }
      }
    },
    switchMainMenu(item) {
      // <router-link class="inner-text" :to="item.menuUrl">{{item.menuname}}</router-link>
      if (item.menuUrl != null) {
        this.$router.push({ path: item.menuUrl });
      }
    }
  }
};
</script>


