<template>
  <div>
    <v-header :menumessage="menumessage"></v-header>
    <v-menu></v-menu>
    <div class="mainBox">
      <div class="main-container">
        <div class="el-row">
          <div class="el-col el-col-24 content_shadow">
          </div>
        </div>
      </div>
    </div>
    <v-footer></v-footer>
  </div>
</template>
<script>
import Headerss from "../common/header";
import Footerss from "../common/footer";
import Menu from "../common/menu";
import Table from "../../utils/tableUtil";
import Echarts from "../../utils/echartsUtil";
export default {
  name: "home_page",
  components: {
    "v-header": Headerss,
    "v-footer": Footerss,
    "v-menu": Menu,
    "v-table": Table,
    "v-echarts": Echarts
  },
  data() {
    return {
      menumessage: [{ name: "首页" }],
    };
  },
  methods: {
    
  }
};
</script>


