<template>
  <div id="table_examine">
    <template>
    <el-table
      ref="multipleTable"
      :data="tableData"
      @selection-change="handleSelectionChange"
      :row-class-name="tableRowClassName"
      @row-dblclick="rowDblclick"
      border
      stripe
      tooltip-effect="light"
      style="width: 100%">
      <el-table-column
        v-if="selection"
        type="selection"
        align="center"
        width="55">
      </el-table-column>
      <el-table-column  
       v-for="(item ,index) in title" :key="index"
      :prop="item.prop"
      :label="item.label"
      :min-width="item.tablewidth"
      :align="item.align"
      :show-overflow-tooltip="true">
      <template slot-scope="scope">
        <slot v-if="item.slot" :name="item.prop" :row='scope.row'></slot>
        <span v-else>{{ scope.row[item.prop] }}</span>
      </template>
      </el-table-column>
      <el-table-column fixed="right" v-if="isoperate"  label="操作" min-width="150" >
        <template slot-scope="scope">
          <div class="table-handle">
            <el-tooltip v-for="(item ,index) in operate" :key="index"  
              class="item" 
              effect="light" 
              @click.native="operateData(scope.row,item)"
              :content="item.funcName" 
              placement="top"
              v-if="statusCheck(scope.row,item)"
              >
              <span :class="item.position"></span>
            </el-tooltip>
          </div>
            </template>
          </el-table-column>  <!-- 操作结束 -->
        </el-table>
        <!-- 表格分页-->
        <div class="table-pagination" v-if="pagenation">
          <div class="pagination-wrap">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              @prev-click="handlePreClick"
              @next-click="handleNextClick"
              :current-page="currentpage"
              :page-sizes="pageSelect"
              :page-size="currentpagesize"
              :total="count"
              layout="total, sizes, prev, pager, next, jumper"
              popper-class="每页"
              prev-text="上一页"
              next-text="下一页"
            ></el-pagination>  
          </div>
        </div>
        <!-- 表格分页 end-->
      </template>
  </div>
</template>
<script>
import Vue from "vue";
export default {
  name: "tableUtil",
  directoryData: [],
  directoryDataL: {},
  data() {
    return {
      currentpage: 0,
      pageSelect: [5,10, 20, 50, 100],
      currentpagesize: this.pagesize,
      groupLevel: this.componentLevel,
      mainKey: this.childMainKey,
    };
  },
  props: {
    count: {
      type: Number,
      default:0
    },
    tableData: {
      type: Array,
      required: true
    },
    title: {
      type: Array,
      required: true
    },
    childMainKey: {
      type: String,
      required: false
    },
    isoperate: {
      type: Boolean,
      required: false
    },
    operate: {
      type: Array,
      required: false
    },
    selection:{
      type:Boolean,
      default:false
    },
    pagenation:{
      type:Boolean,
      default:true
    },
    pagesize:{
      type:Number,
      default:10
    },
    componentLevel:{  //控件嵌套层级
      type:Number,
      required: false,
      default:1
    }
  },
  created:function(){
    
  },
  computed:{
  },
  mounted:function(){
  },
  methods:{
    statusCheck(rowdata,item){  //父组件中实现，通过角色权限、数据状态，决定哪些按钮是否展示
      // return this.$parent.statusCheck(rowdata,item);
      if(this.groupLevel == 2){
        return this.$parent.$parent.statusCheck(rowdata,item,this.mainKey);
      }else if(this.groupLevel == 3){
        return this.$parent.$parent.$parent.statusCheck(rowdata,item,this.mainKey);
      }else if(this.groupLevel == 4){
        return this.$parent.$parent.$parent.$parent.statusCheck(rowdata,item,this.mainKey);
      }else if(this.groupLevel == 5){
        return this.$parent.$parent.$parent.$parent.$parent.statusCheck(rowdata,item,this.mainKey);
      }else if(this.groupLevel == 6){
        return this.$parent.$parent.$parent.$parent.$parent.$parent.statusCheck(rowdata,item,this.mainKey);
      }else{
        return this.$parent.statusCheck(rowdata,item,this.mainKey);
      }
    },
    operateData(row,item){  //“操作”按钮的方法 根据item自行判断要执行什么操作
      // this.$parent.operateData(row,item);
      if(this.groupLevel == 2){
        this.$parent.$parent.operateData(row,item,this.mainKey);
      }else if(this.groupLevel == 3){
        this.$parent.$parent.$parent.operateData(row,item,this.mainKey);
      }else if(this.groupLevel == 4){
        this.$parent.$parent.$parent.$parent.operateData(row,item,this.mainKey);
      }else if(this.groupLevel == 5){
        this.$parent.$parent.$parent.$parent.$parent.operateData(row,item,this.mainKey);
      }else if(this.groupLevel == 6){
        this.$parent.$parent.$parent.$parent.$parent.$parent.operateData(row,item,this.mainKey);
      }else{
        this.$parent.operateData(row,item,this.mainKey);
      }
    },
    changePage(currentPage, pagesize){  // 提供给父组件, 修改 分页 页码和size
      if(currentPage){
        this.currentpage = currentPage;
      }
      if(pagesize){
        this.currentpagesize = pagesize;
      }
    },
    handleSizeChange(e) {
      //选择页数
      //console.log("选择pagesize为:" + e);
      this.currentpagesize = e;
      if(this.groupLevel == 2){
        this.$parent.$parent.getData(this.currentpage, this.currentpagesize,this.mainKey);
      }else if(this.groupLevel == 3){
        this.$parent.$parent.$parent.getData(this.currentpage, this.currentpagesize,this.mainKey);
      }else if(this.groupLevel == 4){
        this.$parent.$parent.$parent.$parent.getData(this.currentpage, this.currentpagesize,this.mainKey);
      }else if(this.groupLevel == 5){
        this.$parent.$parent.$parent.$parent.$parent.getData(this.currentpage, this.currentpagesize,this.mainKey);
      }else if(this.groupLevel == 6){
        this.$parent.$parent.$parent.$parent.$parent.$parent.getData(this.currentpage, this.currentpagesize,this.mainKey);
      }else{
        this.$parent.getData(this.currentpage, this.currentpagesize,this.mainKey);
      }
    },
    handleCurrentChange(e) {
      //console.log("当前页码变更:" + e);
      //console.log(this.mainKey);
      this.currentpage = e;
      if(this.groupLevel == 2){
        this.$parent.$parent.getData(this.currentpage, this.currentpagesize,this.mainKey);
      }else if(this.groupLevel == 3){
        this.$parent.$parent.$parent.getData(this.currentpage, this.currentpagesize,this.mainKey);
      }else if(this.groupLevel == 4){
        this.$parent.$parent.$parent.$parent.getData(this.currentpage, this.currentpagesize,this.mainKey);
      }else if(this.groupLevel == 5){
        this.$parent.$parent.$parent.$parent.$parent.getData(this.currentpage, this.currentpagesize,this.mainKey);
      }else if(this.groupLevel == 6){
        this.$parent.$parent.$parent.$parent.$parent.$parent.getData(this.currentpage, this.currentpagesize,this.mainKey);
      }else{
        this.$parent.getData(this.currentpage, this.currentpagesize,this.mainKey);
      }
    },
    handlePreClick(e) {
      //console.log(e);
    },
    handleNextClick(e) {
      //console.log(e);
    },
    handleSelectionChange(val){//多选框勾选触发方法,自定义自己需要的实现逻辑
      this.$parent.selectionChange(val);
    },
    tableRowClassName({row}){
      try {
        if(this.groupLevel == 2){
          return this.$parent.$parent.tableRowClassName(row,this.mainKey);
        }else if(this.groupLevel == 3){
          return this.$parent.$parent.$parent.tableRowClassName(row,this.mainKey);
        }else if(this.groupLevel == 4){
          return this.$parent.$parent.$parent.$parent.tableRowClassName(row,this.mainKey);
        }else if(this.groupLevel == 5){
          return this.$parent.$parent.$parent.$parent.$parent.tableRowClassName(row,this.mainKey);
        }else if(this.groupLevel == 6){
          return this.$parent.$parent.$parent.$parent.$parent.$parent.tableRowClassName(row,this.mainKey);
        }else{
          return this.$parent.tableRowClassName(row,this.mainKey);
        }
        // return this.$parent.tableRowClassName(row);
      }catch(e){
        return;
      }
    },
    rowDblclick(row){
      try{
        if(this.groupLevel == 2){
          return this.$parent.$parent.rowDblclick(row,this.mainKey);
        }else if(this.groupLevel == 3){
          return this.$parent.$parent.$parent.rowDblclick(row,this.mainKey);
        }else if(this.groupLevel == 4){
          return this.$parent.$parent.$parent.$parent.rowDblclick(row,this.mainKey);
        }else if(this.groupLevel == 5){
          return this.$parent.$parent.$parent.$parent.$parent.rowDblclick(row,this.mainKey);
        }else if(this.groupLevel == 6){
          return this.$parent.$parent.$parent.$parent.$parent.$parent.rowDblclick(row,this.mainKey);
        }else{
          return this.$parent.rowDblclick(row,this.mainKey);
        }
        // return this.$parent.rowDblclick(row);
      }catch (e){
        return ;
      }

    }
  }
};
</script>


