<template>
  <!-- <v-header :menumessage="menumessage"></v-header> -->
  <div class="mainBox">
    <div class="el-row fileManage">
      <div class="el-col el-col-24 page_content_top">
        <div class="left pull-left">
          <span :class="['forward' , forwardStack.length()<=1?'disable':'']"  @click="pathback">
            <span class="icon-forword"></span>
          </span>
          <span :class="['back' , backStack.length()<=0?'disable':'']" @click="pathForward">
            <span class="icon-back"></span>
          </span>
          <el-input v-model="inputPath" placeholder="请输入内容" v-show="!showDict"  @change="inputPathStr()"  @blur="inputPath=null,showDict=true"></el-input>
          <el-breadcrumb separator-class="el-icon-arrow-right" v-show="showDict">
            <!-- <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>活动管理</el-breadcrumb-item>
            <el-breadcrumb-item>活动列表</el-breadcrumb-item>
            <el-breadcrumb-item>活动详情</el-breadcrumb-item>-->
            <el-breadcrumb-item v-for="(item,index) in dataSourcePath" :key="index" @click="breadcrumbClick">
              <div @click="breadcrumbClick(index)">{{item}}</div></el-breadcrumb-item>
          </el-breadcrumb>
          <div class="btn-changeShow" @click="changeDict()" v-show="showDict"></div>
        </div>
        <div class="right pull-right">
          <span class="showNum">最多显示</span>
          <el-select v-model="pageSize" placeholder="1000">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <span class="showNum">个项目</span>
        </div>
      </div>
      <div class="el-col el-col-24 page_content_cont">
        <div class="el-col el-col-24 content-left">
          <div class="block">
            <v-tree
              ref="treeChild"
              :treeData="dataSourceOptions"
              :ParentLoadNode="loadNode"
              :node_click="node_click"
            ></v-tree>
          </div>
        </div>
        <div class="el-col el-col-24 content-right">
          <!-- <div class="folder">
            <div class="folder-img"></div>
            <span class="folderName">agent</span>
          </div>
          <div class="file">
            <div class="file-img"></div>
            <span class="fileName">file1</span>
          </div> -->
          <!--文件夹部分 -->
          <div v-for="item in dataSourceDetailRightFolder" class="folder"  v-show="dataSourceDetailRightFolder">
            <div class="folder-img" :title="item.label"  @dblclick="dblclick(item.data)"></div>
            <span
              class="folderName"
              @dblclick="dblclick(item.data)"
            >{{item.data.label}}</span>
          </div>

          <!-- 文件部分 -->
          <div v-for="item in dataSourceDetailRightFile" class="folder"  v-show="dataSourceDetailRightFolder">
            <div class="file-img" :title="item.label" @dblclick="fileDblclick(item)"></div>
            <span
              class="fileName"
              @dblclick="fileDblclick(item)"
              :title="item.label"
            >{{item.label}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// import Headerss from "./common/header";
// import Footerss from "./common/footer";
import Table from "../../utils/tableUtil";
import Echarts from "../../utils/echartsUtil";
import Tree from "../../utils/tree";
import  Stack  from  "../../utils/Stack"

export default {
  name: "fileManage",
  components: {
    // "v-header": Headerss,
    // "v-footer": Footerss,
    "v-tree":Tree,
    "v-table": Table,
    "v-echarts": Echarts
  },
  data() {
    return {
      inputPath: '',
        options: [{  //单页拉取多少条数据
            value: '选项1',
            label: '1000'
          }, {
            value: '选项2',
            label: '2000'
          }, {
            value: '选项3',
            label: '3000'
          }, {
            value: '选项4',
            label: '4000'
          }, {
            value: '选项5',
            label: '5000'
          }],

        value: '',
        dataSourceOptions:[],
        
        dataSourceDetail:{},
        dataSourceDetailRightFolder:[],
        dataSourceDetailRightFile:[],
        dataSourcePath:[],
        subTrees:[],
        defaultProps: {
          children: 'children',
          label: 'label',
          icon: 'icon'
        },
        jumpFlag:0,//默认为0 表示是正常的访问请求，当通过前进后退按钮操作时，该值为1，不记录操作历史；
        forwardStack: new Stack,//正常访问历史
        backStack:new Stack,  //回退时的堆栈记录
        baseUrl:null,
        showDict:true, //是否展示面包屑导航   用于面包屑与input的切换
        pageSize:1000
    };
  },
  created:function(){
    this.baseUrl  = process.env.baseUrl;
    this.getSRInfo();
  },
  methods: {
    handleNodeClick(data) {
      console.log(data);
    },
    getSRInfo(){
      var param = {
        // queryId: fileId
      };
      this.$get("/catalog/sr", param)
        .then(res => {
          // console.log(res);
          // console.log(this.dataSourceOptions);
          let  reaults = res;
          for (let i in res) {
            // console.log(res[i]);
            // this.dataSourceOptions.push({});
            let  SRnode  = {};
            SRnode.icon='icon-root';
            SRnode.label=res[i];
            SRnode.SRname=res[i];
            SRnode.children=[];
            SRnode.path="/"+res[i];//选中展示面包屑使用
            SRnode.id  = SRnode.path;
            SRnode.address=[]; //定位当前元素在整个树中的位置
            this.dataSourceOptions.push(SRnode);
            let node1 = this.$refs.treeChild.getNode(SRnode.path );
            this.dataSourceDetailRightFolder.push({"data":SRnode});
            console.log("SRname22222222222",node1);
            }
            // console.log(this.dataSourceOptions);
            }
            )
        .catch(() => {});
    },
    
    //懒加载模式中 点击或者展开时 拉取子节点数据的方法
    loadNode(node,resolve ){
      if (node.level === 0) {
          return resolve([]);
        }

      let tempPath  =node.data.path.substring(1) ;
      let myPath;
      if(tempPath.indexOf('/')  ==-1){
        myPath= '/'
      }else {
        myPath=tempPath.substr(tempPath.indexOf('/'));
      }

      let parentPath=node.data.path;
      let parentAddress=node.data.address;
      let parentName=node.data.label;
      let SRname = node.data.SRname;
      parentAddress.push(parentName);

      var param = {
        srName:SRname,
        filePath:myPath,
        pageNumber:1,
        pageSize:this.pageSize
      };
      let subTrees=[];
      let subTreeDetail = [];
        this.$get("/catalog/list", param)
        .then(res => {
          // console.log(res);
           let  results = res;
          
          for (let i in results) {
            
            let subTree = {};
            subTree.icon='filey';
            subTree.label=results[i].name;
            subTree.children=[];
            subTree.path=parentPath + '/' + results[i].name;
            subTree.id  = subTree.path;
            subTree.address=this.$tools.deepClone(parentAddress);//深复制，避免数据的公用混乱
            
            subTree.SRname  = SRname;
            
            if(results[i].nodeType == 0){
              subTrees.push(subTree);
              }else{
                subTreeDetail.push(subTree);
              }
            
            }
            
            this.dataSourceDetail[node.id]=subTreeDetail;
            
            resolve(subTrees);
            //当第一次点击时，点击事件不能获取到node，只有加载完数据之后再做一个数据传递
            this.dataSourceDetailRightFile=subTreeDetail;
            this.dataSourcePath=parentAddress;
            this.dataSourceDetailRightFolder=node.childNodes;
          }).catch(() => {});    
    },
    node_click(data, node){ //路径上的单击事件  用于切换页面上展示的信息
      this.dataSourcePath=node.data.address;
      this.dataSourceDetailRightFile = this.dataSourceDetail[node.id];
      this.dataSourceDetailRightFolder=node.childNodes;
      if(this.jumpFlag==0){
        this.forwardStack.push(node.data.path);
      }
      
    },
    dblclick(data){//双击事件   在右侧图标形式展示时，双击打开
      let node = this.$refs.treeChild.getNode(data);
      this.node_click(data, node);
      // let node1 = this.$refs.treeChild.getNode(node.data );
      let node1  = this.$refs.treeChild.getCurrentNode();
      console.log("dangqia你",node1);
      console.log(data.path);
          this.$refs.treeChild.setCurrentKey(data.path);
      node1  = this.$refs.treeChild.getNode(data.path);
      console.log("dangqian2",node1);
      // this.queryChild(node,2);
      node1.expand();
    },
    breadcrumbClick(index){   //面包屑上的单击切换路径事件
      console.log("触发面包屑逻辑");
      let path="";
      let node1 ;
      for (let  i  in this.dataSourcePath) { //从顶级目录开始 到选中的目录都打开
        if(i <= index){
          path=path + "/" + this.dataSourcePath[i];
          node1  = this.$refs.treeChild.getNode(path);
          node1.expand();
          console.log(i);
        }
      }
      console.log(path);
    
        console.log("dangqian2",node1);
        // this.queryChild(node,2);
        this.$refs.treeChild.setCurrentKey(path);
        this.node_click(node1.data,node1);
        node1.expand();
    },
    inputPathStr(){
      let tempPath  =this.inputPath.substring(1) ;
      let myPath;
      let mySrName;
      if(tempPath.indexOf('/')  ==-1){
        myPath= '/'
        mySrName=tempPath
      }else {
        myPath=tempPath.substr(tempPath.indexOf('/'));
        mySrName=tempPath.split('/')[1]
      }
      let param  = {
        srName:mySrName,
        filePath:myPath
      }
      this.$get("/catalog/isFile", param)
        .then(res => {
            if(res==true){   //为文件
              let a = document.createElement('a')
              a.href =this.baseUrl + "/catalog/get?srName=" +mySrName + "&filePath=" + myPath;
              a.click();
            }else if(res==false){   //为目录
              this.openPath();
            }
            this.showDict=true;

        })
        .catch(() => {
          this.$message.warning("输入路径有问题，请确认后重新输入！")
        });

    },
    openPath(){//打开指定的目录 需要从顶级遍历打开
      if(this.inputPath){
        let  pathList = this.inputPath.substr(1).split('/');
        let  node1;
        console.log(pathList);
        let tempPath="";
        for (let  i in pathList ){
          tempPath=tempPath + "/" + pathList[i];
          node1  = this.$refs.treeChild.getNode(tempPath);
          if(!node1){
            this.warningMsg(tempPath + " 指定的目录不存在！")
            return ;
          }
          this.$refs.treeChild.setCurrentKey(tempPath);
          node1.expand();
          
        }
        this.node_click(node1.data,node1);
      }
      this.showDict=true;
      return;


    },
    warningMsg(msg) {
      if (!msg) {
        msg = "操作异常";
      }
      this.$message({
        showClose: true,
        message: msg,
        type: "warning"
      });
    },
    successMsg(msg) {
      if (!msg) {
        msg = "操作成功";
      }
      this.$message({
        showClose: true,
        message: msg,
        type: "success"
      });
    },
    pathForward(){//路径访问记录 正常记录
      if(this.backStack.length()<=0){
        return;
      }
      this.jumpFlag=1;
      this.inputPath  = this.backStack.pop();
      this.forwardStack.push(this.inputPath);
      this.openPath();
      this.jumpFlag=0;
    },
    pathback(){ //返回上一次访问的目录
      if(this.forwardStack.length()<=1){//访问记录 最后一个即为当前的 不需要回退
          return;
        }
      this.jumpFlag=1;
      this.backStack.push(this.forwardStack.pop());
      this.inputPath  = this.forwardStack.peek();
      this.openPath();
      this.jumpFlag=0;
    },
    fileDblclick(data){  //文件上的双击事件，双击下载
      

      let tempPath  =data.path.substring(1) ;
      let myPath;
      if(tempPath.indexOf('/')  ==-1){
        myPath= '/'
      }else {
        myPath=tempPath.substr(tempPath.indexOf('/'));
      }
      let SRname =data.SRname;


       
      let a = document.createElement('a')
      a.href =this.baseUrl + "/catalog/get?srName=" +SRname + "&filePath=" + myPath
      a.click();
      
    },
	  changeDict(){  //点击 面包屑导航之后区域 用于切换面包屑与input的效果
      this.showDict = false ;
      this.$refs.myInput.focus();
    }
  }
};
</script>



