export default {
  // deepClone(target) {
  //     return JSON.parse(JSON.stringify(target))
  // }
  // 定义一个深拷贝函数  接收目标target参数
  deepClone(target) {
    // 定义一个变量
    let result;
    // 如果当前需要深拷贝的是一个对象的话
    if (typeof target === 'object') {
      // 如果是一个数组的话
      if (Array.isArray(target)) {
        result = []; // 将result赋值为一个数组，并且执行遍历
        for (let i in target) {
          // 递归克隆数组中的每一项
          result.push(this.deepClone(target[i]))
        }
        // 判断如果当前的值是null的话；直接赋值为null
      } else if (target === null) {
        result = null;
        // 判断如果当前的值是一个RegExp对象的话，直接赋值    
      } else if (target.constructor === RegExp) {
        result = target;
      } else {
        // 否则是普通对象，直接for in循环，递归赋值对象的所有值
        result = {};
        for (let i in target) {
          result[i] = this.deepClone(target[i]);
        }
      }
      // 如果不是对象的话，就是基本数据类型，那么直接赋值
    } else {
      result = target;
    }
    // 返回最终结果
    return result;
  },

  validStrong(param) {
    var newPassword = param.newPassword;
    //是否只含有数字或字母或特殊字符中的两种
    var regex = /(?!^(\d+|[a-zA-Z]+|[`~!@#$%^&*()_\-+=<>?:"{}|,.\/;'[\]·]+)$)^[\w`~!@#\\\\$%^&*()_\-+=<>?:"{}|,.\/;'[\]·]{1,50}$/;
    //是否只包含数字
    var regexNum = /^[0-9]*$/;
    //是否只包含字母
    var regexChar = /^[A-Za-z]+$/;
    //是否包含特殊字符
    var regexSpecial = /[`~!@#$%^&*()_\-+=<>?:"{}|,.\/;'[\]·]+$/;

    // var regex=/[a-z0-9A-Z]|[`~!@#$%^&*()_\-+=<>?:"{}|,.\/;'[\]·]/;

    var l = newPassword.length;
    if (!newPassword) {
      param['passwordErrorMsg'] = "";
      param['weak'] = "";
      param['center'] = "";
      param['strong'] = "";
      param['password_strength'] = false;
      param['status'] = false;
      return param;
    }
    // if (!regex.test(this.newPassword)) {
    //   this.passwordErrorMsg = "密码组合数字+大小写字母+标点符号！";
    //   this.password_strength = false;
    //   return false;
    // } else {
    if (l < 10) {
      // this.passwordType = "密码强度: 弱";
      param['passwordErrorMsg'] = "密码强度需达到中级及以上！";
      param['weak'] = "weak";
      param['center'] = "";
      param['strong'] = "";
      param['password_strength'] = false;
      param['status'] = false;
      return param;
    }
    if (l >= 10 && l <= 12) {
      if (!regex.test(newPassword)) {
        //没有包含两种以及以上
        // this.passwordType = "密码强度: 弱";
        param['passwordErrorMsg'] = "密码组合数字+大小写字母+标点符号,至少两种组合！";
        param['weak'] = "weak";
        param['center'] = "";
        param['strong'] = "";
        param['password_strength'] = false;
        param['status'] = false;
        return param;
      } else {
        // this.passwordType = "密码强度: 中";
        param['passwordErrorMsg'] = "";
        param['weak'] = "";
        param['center'] = "center";
        param['strong'] = "";
        param['password_strength'] = true;
        param['status'] = true;
        return param;
      }
    }
    if (l > 12) {
      if (!regex.test(newPassword)) {
        //只包含数字或者只包含字母或者只包含特殊字符
        // this.passwordType = "密码强度: 弱";
        param['passwordErrorMsg'] = "密码组合数字+大小写字母+标点符号,至少两种组合！";
        param['weak'] = "weak";
        param['center'] = "";
        param['strong'] = "";
        param['password_strength'] = false;
        param['status'] = false;
        return param;
      } else {
        // this.passwordType = "密码强 度: 强";
        param['passwordErrorMsg'] = "";
        param['weak'] = "";
        param['center'] = "";
        param['strong'] = "strong";
        param['password_strength'] = true;
        param['status'] = true;
        return param;
      }
    }
    // }
  },
  //将字节转为KB等,a为数值,b为小说点保留位数
  formatBytes(a, b) {
    if (0 == a) {
      return "0 Bytes";
    }
    var c = 1024,
    d = b || 2,
    e = ["Bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"],
    f = Math.floor(Math.log(a) / Math.log(c));
    var num={
      "value":parseFloat((a / Math.pow(c, f)).toFixed(d)),
      "unit":e[f]
    }
    // return parseFloat((a / Math.pow(c, f)).toFixed(d)) + " " + e[f];
    return num;
  },
  //时间处理年月日时分秒
  getTime(timeLong) {
    var now = new Date(parseFloat(timeLong));
    var year = now.getFullYear();
    var month = now.getMonth() + 1;
    month = (month + "").length == 1 ? "0" + month : month;
    var date = now.getDate();
    date = (date + "").length == 1 ? "0" + date : date;
    var hour = now.getHours();
    hour = (hour + "").length == 1 ? "0" + hour : hour;
    var minute = now.getMinutes();
    minute = (minute + "").length == 1 ? "0" + minute : minute;
    var second = now.getSeconds();
    second = (second + "").length == 1 ? "0" + second : second;
    // return year + "-" + month+"-" + date +" "+ hour +":"+ minute+":" + second;
    // return year + "-" + month+"-" + date +" "+ hour +":"+ minute;
    return hour +":"+ minute;
  },
  //时间处理年月日时分秒
  getCurrentTime() {
    var now = new Date();
    var year = now.getFullYear();
    var month = now.getMonth() + 1;
    month = (month + "").length == 1 ? "0" + month : month;
    var date = now.getDate();
    date = (date + "").length == 1 ? "0" + date : date;
    var hour = now.getHours();
    hour = (hour + "").length == 1 ? "0" + hour : hour;
    var minute = now.getMinutes();
    minute = (minute + "").length == 1 ? "0" + minute : minute;
    var second = now.getSeconds();
    second = (second + "").length == 1 ? "0" + second : second;
    return year + "-" + month+"-" + date +" "+ hour +":"+ minute+":" + second;
    // return year + "-" + month+"-" + date +" "+ hour +":"+ minute;
    // return hour +":"+ minute;
  },
};



