<template>
  <!-- <v-header :menumessage="menumessage"></v-header> -->
  <div class="mainBox">
    <div class="el-row fileManage ZKInfo">
      <div class="el-col el-col-24 page_content_top">
        <div class="left pull-left">
          <span :class="['forward' , forwardStack.length()<=1?'disable':'']"  @click="pathback">
            <span class="icon-forword"></span>
          </span>
          <span :class="['back' , backStack.length()<=0?'disable':'']" @click="pathForward">
            <span class="icon-back"></span>
          </span>
          <el-breadcrumb separator-class="el-icon-arrow-right" >
            <el-breadcrumb-item v-for="(item,index) in ZKPath" :key="index" @click="breadcrumbClick" v-show="index >=(ZKPathLen-5)"> 
              <div @click="breadcrumbClick(index)" >{{item}}</div></el-breadcrumb-item>
          </el-breadcrumb>
        </div> 
        <!--
        <div class="right pull-right">
          <span class="showNum">最多显示</span>
          <el-select v-model="value" placeholder="1000">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <span class="showNum">个项目</span>
        </div>   -->
      </div>
      <div class="el-col el-col-24 page_content_cont">
        <div class="el-col el-col-24 content-left">
          <div class="block">
            <v-tree
              ref="treeChild"
              :treeData="ZKOptions"
              :ParentLoadNode="loadNode"
              :node_click="node_click"
            ></v-tree>
          </div>
        </div>
        <div class="el-col el-col-24 content-right" >
          <div v-for="item in ZKDetailRight" class="msgFolder"  v-show="true">
            <div :class="item.hasValue==true?'msgFolder-img':'folder-img'" @dblclick="dblclick(item)">
              <el-popover
                    placement="right-end"
                    width="400"
                    trigger="click"
                    @show="msgCliek(item)"
                    v-if="item.hasValue">
                    <div class="floatMsg">
                      <div class="blue-top"></div>
                      <div class="content">
                        <h4>查看节点信息</h4>
                        <div class="nodeMsg">
                          <span class="conMsg">{{nodeData}}
                          </span>
                        </div>
                      </div>
                    </div>
                    <span class="icon-information" slot="reference" @click="msgCliek(item)"></span>
              </el-popover>
            
            </div><span
              class="folderName"
              @dblclick="dblclick(item)"
            >{{item.label}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Table from "../../utils/tableUtil";
import Echarts from "../../utils/echartsUtil";
import Tree from "../../utils/tree";
import  Stack  from  "../../utils/Stack"

export default {
  name: "ZKManage",
  components: {
    "v-tree":Tree,
    "v-table": Table,
    "v-echarts": Echarts
  },
  data() {
    return {
      input: '',
      options: [{
          value: '选项1',
          label: '1000'
        }, {
          value: '选项2',
          label: '2000'
        }, {
          value: '选项3',
          label: '3000'
        }, {
          value: '选项4',
          label: '4000'
        }, {
          value: '选项5',
          label: '5000'
        }],
      value: '',
      ZKOptions: [],
      ZKDetail:{},
      ZKDetailRight:[],
      ZKPath:null,
      ZKPathLen:0,
      ZKDetailRightFolder:null,
      jumpFlag:0,//默认为0 表示是正常的访问请求，当通过前进后退按钮操作时，该值为1，不记录操作历史；
      forwardStack: new Stack,//正常访问历史
      backStack:new Stack,  //回退时的堆栈记录
      nodeData:null,
    };
  },
  created:function(){
    this.getRootInfo();
  },
  methods: {
    handleNodeClick(data) {
      console.log(data);
    },
    getRootInfo(){ //查询ZK的根节点信息
      var param = {
        // queryId: fileId
      };
      this.$get("/zookeeper/root", param)
        .then(res => {
          // console.log(res);
          // console.log(this.dataSourceOptions);
          let  reaults = res;
            // console.log(res[i]);
            // this.dataSourceOptions.push({});
            let  RootNode  = {};
            RootNode.icon='icon-zk-tree';
            RootNode.label=res.name;
            RootNode.name=res.name;
            RootNode.children=[];
            RootNode.path="/"+res.name;//选中展示面包屑使用
            RootNode.id  = RootNode.path;
            RootNode.address=[]; //定位当前元素在整个树中的位置
            this.ZKOptions.push(RootNode);
            // let node1 = this.$refs.treeChild.getNode(RootNode.path );
            this.ZKDetailRight.push(RootNode);
            console.log("SRname22222222222",this.ZKDetailRight);
           
            // console.log(this.dataSourceOptions);
          }
        )
        .catch(() => {});
    },
    
    //懒加载模式中 点击或者展开时 拉取子节点数据的方法
    loadNode(node,resolve ){
      if (node.level === 0) {
          return resolve([]);
        }
      let parentPath=node.data.path;
      let parentAddress=node.data.address;
      let parentName=node.data.label;
      // let SRname = node.data.SRname;
      parentAddress.push(parentName);

      var param = {
        // srName:SRname,
        nodePath:parentPath
      };
      let subTrees=[];
      let subTreeDetail = [];
        this.$get("/zookeeper/list/", param)
        .then(res => {
          // console.log(res);
           let  results = res;
          
          for (let i in results) {
            
            let subTree = {};
            if(results[i].hasValue == true){
              subTree.icon='task';
            }else {
              subTree.icon='filey';
            }
            subTree.hasValue=results[i].hasValue ;
            subTree.label=results[i].name;
            subTree.children=[];
            subTree.path=parentPath + '/' + results[i].name;
            subTree.id  = subTree.path;
            subTree.address=this.$tools.deepClone(parentAddress);//深复制，避免数据的公用混乱
            
            // subTree.SRname  = SRname;
            
            
            subTrees.push(subTree);
            
            
            }
            
            this.ZKDetail[node.id]=subTrees;
            
            resolve(subTrees);
            //当第一次点击时，点击事件不能获取到node，只有加载完数据之后再做一个数据传递
            this.ZKDetailRight=subTrees;
            this.ZKPath=parentAddress;
            this.ZKPathLen = this.ZKPath.length;
          }).catch(() => {});    
    },
    node_click(data, node){ //路径上的单击事件  用于切换页面上展示的信息
      this.ZKPath=node.data.address;
      if(this.ZKPath){
        this.ZKPathLen = this.ZKPath.length;
      }
      //  this.ZKPathLen = this.ZKPath.length();
      this.ZKDetailRight = this.ZKDetail[node.id];
      // this.ZKDetailRight=node.childNodes;
      if(this.jumpFlag==0){
        this.forwardStack.push(node.data.path);
      }
      
    },
    dblclick(data){//双击事件   在右侧图标形式展示时，双击打开
      let node = this.$refs.treeChild.getNode(data);
      this.node_click(data, node);
      // let node1 = this.$refs.treeChild.getNode(node.data );
      let node1  = this.$refs.treeChild.getCurrentNode();
      console.log("dangqia你",node1);
      console.log(data.path);
          this.$refs.treeChild.setCurrentKey(data.path);
      node1  = this.$refs.treeChild.getNode(data.path);
      console.log("dangqian2",node1);
      // this.queryChild(node,2);
      node1.expand();
    },
    breadcrumbClick(index){   //面包屑上的单击切换路径事件
      console.log("触发面包屑逻辑");
      let path="";
      let node1 ;
      for (let  i  in this.ZKPath) { //从顶级目录开始 到选中的目录都打开
        if(i <= index){
          path=path + "/" + this.ZKPath[i];
          node1  = this.$refs.treeChild.getNode(path);
          node1.expand();
          console.log(i);
        }
      }
      console.log(path);
    
        console.log("dangqian2",node1);
        // this.queryChild(node,2);
        this.$refs.treeChild.setCurrentKey(path);
        this.node_click(node1.data,node1);
        node1.expand();
    },
    openPath(){//打开指定的目录 需要从顶级遍历打开
      if(this.inputPath){
        let  pathList = this.inputPath.substr(1).split('/');
        let  node1;
        console.log(pathList);
        let tempPath="";
        for (let  i in pathList ){
          tempPath=tempPath + "/" + pathList[i];
          node1  = this.$refs.treeChild.getNode(tempPath);
          if(!node1){
            this.warningMsg(tempPath + " 指定的目录不存在！")
            return ;
          }
          this.$refs.treeChild.setCurrentKey(tempPath);
          node1.expand();
          
        }
        this.node_click(node1.data,node1);
      }
      return;


    },
    warningMsg(msg) {
      if (!msg) {
        msg = "操作异常";
      }
      this.$message({
        showClose: true,
        message: msg,
        type: "warning"
      });
    },
    successMsg(msg) {
      if (!msg) {
        msg = "操作成功";
      }
      this.$message({
        showClose: true,
        message: msg,
        type: "success"
      });
    },
    pathForward(){//路径访问记录 正常记录
      if(this.backStack.length()<=0){
        return;
      }
      this.jumpFlag=1;
      this.inputPath  = this.backStack.pop();
      this.forwardStack.push(this.inputPath);
      this.openPath();
      this.jumpFlag=0;
    },
    pathback(){ //返回上一次访问的目录
      if(this.forwardStack.length()<=1){//访问记录 最后一个即为当前的 不需要回退
          return;
        }
      this.jumpFlag=1;
      this.backStack.push(this.forwardStack.pop());
      this.inputPath  = this.forwardStack.peek();
      this.openPath();
      this.jumpFlag=0;
    },
    msgCliek(item){//点击展示节点的明细信息时，触发的事件
    this.nodeData = '数据查询中，请稍等。。';
      var param = {
        nodePath: item.path
      };
      this.$get("/zookeeper/data/", param)
        .then(res => {
          let  results = res;
          this.nodeData = results.data;
          }
        )
        .catch(() => {});
      console.log('aaa');

    }
    
  }
};
</script>


