<template>
  <div class="tree-Box">
    <el-tree accordion 
             :data="treeData"  
             :load="loadNode"
             lazy
             icon-class="el-icon-arrow-right"
             node-key="id" 
             ref="tree" 
             @node-click="click" 
             @node-contextmenu="right_click" 
             empty-text="无数据"
             highlight-current>
      <span slot-scope="{ node, data }">
        <span></span>
          <i :class="data.icon"></i> 
          {{ data.label }}
          <!-- 这个buttn中的style 前端在 写样式的时候 删掉它 -->
          <!-- <button v-text="data.btnText?data.btnText:''" :class="data.btnClass" :title="data.btnTip" v-if="data.btnShow" @click.stop="btnClick(data)" style="background: #0070ff;width: 46px;outline: none;border: 1px solid #0070ff;margin-left: 20px;"></button> -->
        </span>
      </span>
    </el-tree>
  </div>
</template>
<script>
export default {
  name: "tree",
  data() {
    return {
    };
  },
  methods: {
    click(data, node, com) {
      if (this.node_click) {
        this.node_click(data,node);
      }
    },
    right_click(e, data, node, com) {
      if (this.node_right_click) {
        this.resetTreeSelect(data.id);
        this.node_right_click(data);
      }
    },
    resetTreeSelect(id) {
      this.$refs.tree.setCurrentKey(id != null && id != undefined && id != "" ? id : null);
    },
    btnClick(data) {
      if (this.nodeBtnClick != null) {
        this.nodeBtnClick(data);
      }
    },
    loadNode(data,resolve) {
      if (this.ParentLoadNode != null) {
        this.ParentLoadNode(data,resolve);
      }
    },
    //通过key设置当前节点 
    setCurrentKey(key){
        this.$refs.tree.setCurrentKey(key); 
    },
    //通过key获得指定节点 
    getNode(key){
        return this.$refs.tree.getNode(key); 
    },
    getCurrentNode(){
      return this.$refs.tree.getCurrentNode(); 
    },
    append(data,node){
        return this.$refs.tree.append(data,node);
    }
  },
  props: {
    treeData: {
      type: Array,
      required: false
    },
    node_click: {
      type: Function,
      default: null
    },
    node_right_click: {
      type: Function,
      default: null
    },
    nodeBtnClick: {
      type: Function,
      default: null
    },
    ParentLoadNode:{
      type: Function,
      default: null
    }
  }
};
</script>


