<template>
  <div class="mainBox">
    <div class="el-row dashboard">
      <div class="el-col el-col-24 page_content_top">
        <div class="left pull-left">
          <i class="icon-cluster-host"></i>
          <span class="host-state">
            集群主机节点状态(共
            <span class="countNum">{{sumNumber}}</span>台，异常
            <span class="abnormalNum">{{errorNum}}</span>台)
          </span>
          <span class="healthy">健康</span>
          <span class="unhealthy">不健康</span>
          <span class="drop-line">掉线</span>
        </div>
        <div class="right pull-right">
          <el-tooltip class="item" content="刷新" placement="bottom">
            <span class="refresh">
              <span class="icon-refresh" @click="refreshOperate()"></span>
            </span>
          </el-tooltip>
          <span class="update-time">数据更新时间：</span>
          <span class="time">{{updateTime}}</span>
        </div>
      </div>
      <div class="el-col el-col-24 page_content_cont">
        <div class="el-col el-col-24 content_top">
          <template v-for="item in sumNode">
            <el-card class="box-card" :class="healthCheck(item.state)">
              <div slot="header" class="clearfix">
                <span>{{item.hostName}}</span>
              </div>
              <div class="text item">
                <p class="text-con">
                  <span class="text-name">CPU使用率</span>
                  <el-progress :percentage="numberFormate(item.cpuUsage)" :stroke-width="11"></el-progress>
                </p>
                <p class="text-con">
                  <span class="text-name">内存使用率</span>
                  <el-progress :percentage="numberFormate(item.memUsage)" :stroke-width="11"></el-progress>
                </p>
                <p class="text-con">
                  <span class="text-name">磁盘(最大分区)</span>
                  <el-progress :percentage="numberFormate(item.brfsDiskUsage)" :stroke-width="11"></el-progress>
                </p>
                <p class="text-con">
                  <span class="text-name">磁盘(系统分区)</span>
                  <el-progress :percentage="numberFormate(item.systemDiskUsage)" :stroke-width="11"></el-progress>
                </p>
                <p class="text-con">
                  <span class="text-name">regionnode进程</span>
                  <!-- <span class="normal">正常</span> -->
                  <span class="normal" v-if="item.regionNodeState=='ONLINE'">健康</span>
                  <span class="offLine" v-if="item.regionNodeState=='OFFLINE'">掉线</span>
                  <span class="offLine" v-if="item.regionNodeState=='ALERT'">不健康</span>
                </p>
                <p class="text-con">
                  <span class="text-name">datanode进程</span>
                  <!-- <span class="offLine">离线</span> -->
                  <span class="normal" v-if="item.dataNodeState=='ONLINE'">健康</span>
                  <span class="offLine" v-if="item.dataNodeState=='OFFLINE'">掉线</span>
                  <span class="offLine" v-if="item.dataNodeState=='ALERT'">不健康</span>
                </p>
              </div>
            </el-card>
          </template>
        </div>
        <div class="el-col el-col-24 content_bottom">
          <!-- 全部节点总磁盘占用情况 -->
          <el-card class="box-card allNode">
            <div slot="header" class="clearfix">
              <span class="titleName">全部节点总磁盘占用情况</span>
            </div>
            <div class="text chart-box">
              <v-echarts
                ref="chart"
                :seriesType="seriesType"
                :seriesData="seriesData"
                :echartsId="echarts"
                :tooltipTrigger="tooltipTrigger"
                :seriesLabelShow="true"
                :legendShow="true"
                :seriesRoseType="seriesRoseType"
                :colorParam="colorParam"
                :seriesRadius="seriesRadius"
                :legendBottom="legendBottom"
                :legendItemWidth="legendItemWidth"
                :legendItemHeight="legendItemHeight"
              ></v-echarts>
            </div>
          </el-card>
          <!-- 每分钟读写文件数 -->
          <el-card class="box-card read-count">
            <div slot="header" class="clearfix">
              <span class="titleName">每分钟读写文件数</span>
              <div class="timeBox">
                <div class="bgtime" :class="sMinutes=='h'?'select':null">
                  <span class="time" @click="hourSelect()">1h</span>
                </div>
                <div class="bgtime" :class="sMinutes=='d'?'select':null">
                  <span class="time" @click="dateSelect()">1D</span>
                </div>
                <div class="bgtime" :class="sMinutes=='td'?'select':null">
                  <span class="time" @click="threeDSelect()">3D</span>
                </div>
              </div>
            </div>
            <div class="text chart-box">
              <div class="chart-cont">
                <!-- <div class="line-chart-cont">
                  <div class="title-box">
                      <span class="ytitle">Root</span>
                  </div>
                  <div class="line-chart">
                    <v-echarts
                      ref="sumChart"
                      :echartsId="sumChart"
                      :seriesParam="rootSeriesParam"
                      :xAxisData="rootTimeData"
                      :useAxis="true"
                      :yNameGap="yNameGap"
                    ></v-echarts>
                  </div>
                </div>-->
                <template v-for="item in listImageData">
                  <div class="line-chart-cont">
                    <div class="title-box">
                      <span class="ytitle">Root{{item.yAxisName}}</span>
                    </div>
                    <div class="line-chart">
                      <v-echarts
                        ref="bussType"
                        :echartsId="item.echartsId"
                        :seriesParam="item.seriesParam"
                        :xAxisType="xType"
                        :useAxis="true"
                        :axisLabelFormatter="axisLabelFormatter"
                        :yNameGap="yNameGap"
                      ></v-echarts>
                    </div>
                  </div>
                </template>
              </div>
            </div>
          </el-card>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Echarts from "../../utils/echartsUtil";
export default {
  name: "Dashboard",
  components: {
    "v-echarts": Echarts
  },
  data() {
    return {
      sumNode: [], //所有的节点设备数据
      diskNum: null, //磁盘使用数据
      echarts: "echarts",
      seriesType: "pie", //图形类型
      seriesData: [], //饼图数据
      sumNumber: 0, //总台数
      errorNum: 0, //异常台数
      minutes: 60, //距离现在时刻的时间间隔(单位分钟)
      sMinutes: "h", //选择的是哪一个时间范围
      rootTimeData: [], //所有业务的时间
      rootSeriesParam: [], //root的series
      sumChart: "sumChart", //所有业务图的图形id
      // rootYName: "ROOT", //所有业务的y轴名称
      businessList: [], //业务名称集合
      listImageData: [], //动态画图数据
      updateTime: null, //页面数据刷新数据
      tooltipTrigger: "item", //提示框触发类型
      seriesRoseType: "radius", //展示成南丁格尔图
      colorParam: ["#de4343", "#474747"], //饼图颜色
      seriesRadius: ["10%", "50%"], //饼图的半径
      yNameGap: 13, //标题距离y轴距离
      legendBottom: 340, //饼图图例组件离容器上侧的距离
      legendItemWidth: 10, //饼图图例标记的图形宽度
      legendItemHeight: 10, //饼图图例标记的图形高度
      temChartList: {}, //临时对象存储
      xType: "time"
    };
  },
  mounted() {
    this.getSummary();
    this.diskusage();
    this.getSumReadWrite();
  },
  watch: {
    listImageData: {
      deep: true,
      handler(val, oldval) {
        if (this.listImageData.length == this.businessList.length + 1) {
          this.$nextTick(() => {
            this.$refs.bussType[0].resizeOption();
            var value = this.businessList;
            var s = this.$refs.bussType;
            if (
              this.$refs.bussType &&
              value.length == this.$refs.bussType.length - 1
            ) {
              for (var j in value) {
                this.$refs.bussType[j].resizeOption();
              }
            }
          });
        }
      }
    }
  },
  methods: {
    //获取DashBoard页面的节点信息
    getSummary() {
      this.updateTime = this.$tools.getCurrentTime();
      this.$get("dashboard/summary", null)
        .then(res => {
          this.sumNode = res;
          this.sumNumber = this.sumNode.length;
          var errorNum = 0;
          for (var i in res) {
            if (res[i].state != "ONLINE") {
              errorNum++;
            }
          }
          this.errorNum = errorNum;
        })
        .catch(() => {});
    },
    //健康度反显
    healthCheck(value) {
      //state字段有三个值：ONLINE，OFFLINE，ALERT
      //分别对应健康，掉线和不健康
      //class的三个值: healthy-box,drop-line-box,unhealthy-box
      if (value == "ONLINE") {
        return "healthy-box";
      } else if (value == "OFFLINE") {
        return "drop-line-box";
      } else if (value == "ALERT") {
        return "unhealthy-box";
      }
    },
    //使用率小数点后取两位数
    numberFormate(value) {
      if (value != null && value != "") {
        return Number(value.toFixed(2));
      }
      return null;
    },
    //获取磁盘使用情况
    diskusage() {
      this.seriesData = [];
      this.$get("dashboard/diskusage", null)
        .then(res => {
          this.diskNum = res;
          var free = this.$tools.formatBytes(res.free, 2);
          var used = this.$tools.formatBytes(res.used, 2);
          var parmOfUsed = {
            name: "已使用",
            value: used.value
          };
          var parmOfFree = {
            name: "未使用",
            value: free.value
          };
          this.seriesData.push(parmOfUsed);
          this.seriesData.push(parmOfFree);
        })
        .catch(() => {});
    },
    //获取所有业务的读写汇总的统计量
    getSumReadWrite() {
      this.rootSeriesParam = [];
      this.rootTimeData = [];
      var param = {
        minutes: this.minutes
      };
      this.$get("dashboard/stats", param)
        .then(res => {
          var datas = res.datas;
          let rootReadData = [];
          let rootWriteData = [];
          for (var i in datas) {
            // var time = this.$tools.getTime(datas[i].time);
            // this.rootTimeData.push(time);
            var time = this.$tools.getTime(datas[i].time);
            rootReadData.push([datas[i].time, datas[i].data.read]);
            rootWriteData.push([datas[i].time, datas[i].data.write]);
          }
          this.rootSeriesParam = [
            {
              //图形类型
              type: "line",
              name: "read",
              data: rootReadData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer",
              color: "#00c0ff",
            },
            {
              //图形类型
              type: "line",
              name: "write",
              data: rootWriteData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer",
              color: "#f8c516",
            }
          ];
          this.listBusiness();
        })
        .catch(() => {});
    },
    //点击选择一小时
    hourSelect() {
      this.minutes = 60;
      this.sMinutes = "h";
      this.getSumReadWrite();
    },
    //点击选择一天
    dateSelect() {
      this.minutes = 60 * 24;
      this.sMinutes = "d";
      this.getSumReadWrite();
    },
    //点击选择三天
    threeDSelect() {
      this.minutes = 60 * 24 * 3;
      this.sMinutes = "td";
      this.getSumReadWrite();
    },
    //查询所有的业务类型
    listBusiness() {
      this.$get("dashboard/business", null)
        .then(res => {
          this.businessList = res;
          this.listMesgByType(res);
        })
        .catch(() => {});
    },
    //循环遍历业务类型获取对应的读写数据
    listMesgByType(value) {
      var _this = this;
      _this.listImageData = [];
      var d = _this.rootSeriesParam;
      var params = {
        echartsId: "sumChart",
        seriesParam: _this.rootSeriesParam,
        // xAxisData: _this.rootTimeData,
        yAxisName: ""
      };
      _this.listImageData.push(params);
      for (var i in value) {
        var param = {
          minutes: _this.minutes
        };
        _this.listReadWriteByType(value[i], param);
      }
    },
    //获取不同类型的业务数据
    listReadWriteByType(type, param) {
      this.$get("dashboard/stats/" + type, param)
        .then(res => {
          this.drawImageByType(res, type);
        })
        .catch(() => {});
    },
    //根据不同的业务类型封装数据
    drawImageByType(value, type) {
      var _this = this;
      var datas = value.datas;
      let timeData = [];
      let readData = [];
      let writeData = [];
      for (var i in datas) {
        // var time = _this.$tools.getTime(datas[i].time);
        // timeData.push(time);
        readData.push([datas[i].time,datas[i].data.read]);
        writeData.push([datas[i].time,datas[i].data.write]);
      }
      let seriesParam = [
        {
          //图形类型
          type: "line",
          name: "read",
          data: readData,
          symbol: "emptyCircle",
          symbolSize: 6,
          showSymbol: true,
          cursor: "pointer",
          color: "#00c0ff"
        },
        {
          //图形类型
          type: "line",
          name: "write",
          data: writeData,
          symbol: "emptyCircle",
          symbolSize: 6,
          showSymbol: true,
          cursor: "pointer",
          color: "#f8c516"
        }
      ];
      var param = {
        echartsId: value.business,
        seriesParam: seriesParam,
        // xAxisData: timeData,
        yAxisName: "/" + value.business
      };
      _this.listImageData.push(param);
    },
    //点击刷新页面
    refreshOperate() {
      this.getSummary();
      this.diskusage();
      this.getSumReadWrite();
    },
    axisLabelFormatter: function(value, index) {
      var date = new Date(value);
      var texts = [date.getHours(), date.getMinutes()];
      return texts.join(":");
    }
  }
};
</script>


