<!-- 修改密码弹框 -->
<el-dialog title="修改密码" :visible.sync="updatePassDialogVisible" :close-on-click-modal="false"
            :append-to-body="true" center custom-class="updatePass_dialog" top="calc(50vh - 180px)"
            @close="updatePassDialogShow(false)">
    <div class="layer_content">
      <div class="form_group">
        <label class="layer_leftInfo">
          <span class="red-star">*</span>原密码
        </label>
        <el-input type="password" v-model="oldPassword" placeholder="请输入原密码"></el-input>
      </div>
      <div class="form_group">
        <label class="layer_leftInfo">
          <span class="red-star">*</span>新密码
        </label>
        <el-input type="password" v-model="newPassword" placeholder="请输入新密码"></el-input>
      </div>
      <div class="form_group">
        <label class="layer_leftInfo left_strength">密码强度</label>
        <div class="input_box">
          <p class="pass_strength">
            <label id="weak" :class="weak">弱</label>
            <label id="center" :class="center">中</label>
            <label id="strong" :class="strong">强</label>
          </p>
        </div>
      </div>
      <div class="form_group">
        <label class="layer_leftInfo">
          <span class="red-star">*</span>确认密码
        </label>
        <el-input type="password" v-model="newPasswordAgain" placeholder="再次输入新密码"></el-input>
        <label class="error_message" v-text="passwordErrorMsg">密码错误</label>
        <input id="errorPassworldNum" value="1" v-show="false" />
      </div>
      <div class="form_group error_tip">
        <p class="tip_info">密码长度至少10个字符，密码组合数字+字母标准，区分英文大小写</p>
      </div>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="editSave" class="el-button">确定</el-button>
      <el-button type="primary" plain @click="updatePassDialogShow(false)"class="el-button">取消</el-button>
    </span>
    </el-dialog>
    <!-- 遮罩 -->
    <!-- <div class="layer_floating_shade" :style="isShowHeaderShade"></div> -->
    <!-- 修改密码 -->
    <!-- <div class="layer_floating_box layer_pass_box" :style="isShowPasswordBox">
        <p>修改密码<i></i></p>
        <span class="icon-close" @click="operateCancle"></span>
      <div class="layer_content">
        <div class="form_group">
          <label class="layer_leftInfo">
            <span class="red-star">*</span>原密码
          </label>
          <el-input type="password" v-model="oldPassword" placeholder="请输入原密码"></el-input>
        </div>
        <div class="form_group">
          <label class="layer_leftInfo">
            <span class="red-star">*</span>新密码
          </label>
          <el-input type="password" v-model="newPassword" placeholder="请输入新密码"></el-input>
        </div>
        <div class="form_group">
          <label class="layer_leftInfo left_strength">密码强度</label>
          <div class="input_box">
            <p class="pass_strength">
              <label id="weak" :class="weak">弱</label>
              <label id="center" :class="center">中</label>
              <label id="strong" :class="strong">强</label>
            </p>
          </div>
        </div>
        <div class="form_group">
          <label class="layer_leftInfo">
            <span class="red-star">*</span>确认密码
          </label>
          <el-input type="password" v-model="newPasswordAgain" placeholder="再次输入新密码"></el-input>
          <label class="error_message" v-text="passwordErrorMsg">密码错误</label>
          <input id="errorPassworldNum" value="1" v-show="false" />
        </div>
        <div class="form_group error_tip">
          <p class="tip_info">密码长度至少10个字符，密码组合数字+字母标准，区分英文大小写</p>
      </div>
        <div class="layer_button">
          <button type="button" @click="editSave" class="el-button el-button--primary">确定</button>
          <button type="button" @click="operateCancle" class="el-button el-button--default">取消</button>
        </div>
      </div>
    </div> -->
  </div>
  <!-- .header end -->
</template>
<script>
import Vue from "vue";
import { async } from "q";
export default {
  name: "headerss",
  data() {
    return {
      updatePassDialogVisible: false, //修改密码弹框
      userInfo: {
        id: "1",
        username: "admin",
        password: "",
        nickname: "管理员",
        flag: 0,
        email: "test@bonree.com",
        phone: "13234567845",
        inDate: 1564416000000,
        text: "",
        organizationId: "1",
        gender: 0,
        orgName: "测试部门北京",
        orgParentId: 3,
        orgPath: "0",
        roleList: [
          {
            roleId: 1,
            roleName: "管理员",
            roleDesc: "测试管理员",
            createDate: "2019-07-30 00:00:00",
            roleType: 1
          },
          {
            roleId: 110045,
            roleName: "cess",
            roleDesc: "ce",
            createDate: "2019-10-14 18:55:41",
            roleType: 1
          }
        ],
        simpleRoleType: 4
      },
      isShowHeaderShade: { display: "none" },
      isShowPasswordBox: { display: "none" },
      oldPassword: "", //旧密码
      newPassword: "", //新密码
      newPasswordAgain: "", //再次输入新密码
      // passwordType: "", //密码强度类型
      passwordErrorMsg: "", //错误信息
      errorPassworldNum: 1,
      count: 10,
      password_strength: false,
      sameFlag: false,
      weak: "",
      center: "",
      strong: "",
      orginPassword: "",
      routeIndex:null //当前路由的index
    };
  },
  // props: {
  //   menumessage: {
  //     type: Array,
  //     required: true
  //   }
  // },
  created: function() {
    this.loadUserInfo();
    this.flushRoute();
  },
  computed:{
    watchRoute(){
      return this.$route.path;
    }
  },
  watch: {
    newPassword(o1, o2) {
      this.validStrong();
    }, //两次输入的密码是否一致
    newPasswordAgain(o1, o2) {
      this.validSame();
    },
    watchRoute(o,n){
      console.log(this.$route.path)
    if(n =='/'){
      this.routeIndex = "1";
      return;
    }
    if(n =='/fileManage'){
      this.routeIndex = "2";
      return;
    }
    if(n =='/ZKInfo'){
      this.routeIndex = "3";
      return;
    }
    if(n =='/systemMonitor'){
      this.routeIndex = "4";
      return;
    }
    }
  },
  methods: {
    operateCancle() {
      this.isShowHeaderShade = { display: "none" };
      this.isShowPasswordBox = { display: "none" };
      this.oldPassword = "";
      this.newPassword = "";
      this.newPasswordAgain = "";
      // this.passwordType = "";
      this.weak = "";
      this.center = "";
      this.strong = "";
      this.passwordErrorMsg = "";
      this.password_strength = false;
      this.sameFlag = false;
    },
    openMsgBox() {
      this.isShowHeaderShade = { display: "block" };
      this.isShowPasswordBox = { display: "block" };
    },
    //修改密码显示
    updatePassDialogShow(value){
      this.updatePassDialogVisible=value;
    },
    getUserInfo() {
      if (this.userInfo) {
        return this.userInfo;
      } else {
        return this.loadUserInfo();
      }
    },
    loadUserInfo() {
      let userData = this.userInfo;
      var str = JSON.stringify(userData);
      sessionStorage.setItem("userInfo", str);
      if (userData) {
        this.$nextTick(() => {
          this.showUserDetail();
        });
        return (this.userInfo = userData);
      }
    },
    loginOutUrl() {
      return false;
    },
    warningMsg(msg) {
      if (!msg) {
        msg = "操作异常";
      }
      this.$message({
        showClose: true,
        message: msg,
        type: "warning"
      });
    },
    successMsg(msg) {
      if (!msg) {
        msg = "操作成功";
      }
      this.$message({
        showClose: true,
        message: msg,
        type: "success"
      });
    },
    validStrong() {
      //是否只含有数字和字母
      var regex = /^[a-z0-9A-Z]+$/;
      //是否只包含数字
      var regexNum = /^[0-9]*$/;
      //是否只包含字母
      var regexChar = /^[A-Za-z]+$/;
      var l = this.newPassword.length;
      if (!this.newPassword) {
        this.password_strength = false;
        // this.passwordType = "";
        this.weak = "";
        this.center = "";
        this.strong = "";
        return false;
      }
      if (!regex.test(this.newPassword)) {
        this.passwordErrorMsg = "密码组合数字+字母标准，区分英文大小写！";
        this.password_strength = false;
        return false;
      } else {
        if (l < 10) {
          this.passwordErrorMsg = "密码强度需达到中级及以上！";
          // this.passwordType = "密码强度: 弱";
          this.weak = "weak";
          this.center = "";
          this.strong = "";
          this.password_strength = false;
          return false;
        }
        if (l >= 10 && l <= 12) {
          if (
            regexNum.test(this.newPassword) ||
            regexChar.test(this.newPassword)
          ) {
            //只包含数字或者只包含字母
            this.passwordErrorMsg = "密码强度需达到中级及以上！";
            // this.passwordType = "密码强度: 弱";
            this.weak = "weak";
            this.center = "";
            this.strong = "";
            this.password_strength = false;
            return false;
          } else {
            // this.passwordType = "密码强度: 中";
            this.weak = "";
            this.center = "center";
            this.strong = "";
            this.passwordErrorMsg = "";
            this.password_strength = true;
            return true;
          }
        }
        if (l > 12) {
          if (
            regexNum.test(this.newPassword) ||
            regexChar.test(this.newPassword)
          ) {
            //只包含数字或者只包含字母
            this.passwordErrorMsg = "密码强度需达到中级及以上！";
            // this.passwordType = "密码强度: 弱";
            this.weak = "weak";
            this.center = "";
            this.strong = "";
            this.password_strength = false;
            return false;
          } else {
            this.weak = "";
            this.center = "";
            this.strong = "strong";
            // this.passwordType = "密码强 度: 强";
            this.passwordErrorMsg = "";
            this.password_strength = true;
            return true;
          }
        }
      }
    },
    validSame() {
      if (this.password_strength) {
        if (this.newPasswordAgain == this.newPassword) {
          this.passwordErrorMsg = "";
          this.sameFlag = true;
        } else {
          this.passwordErrorMsg = "两次输入密码不一致";
          this.sameFlag = false;
        }
      }
    },
    editSave() {
      this.validSame();
      if (!this.oldPassword || !this.sameFlag || !this.password_strength) {
        this.warningMsg("输入有误");
        return;
      }
      let old = this.oldPassword;
      let newPwd = this.newPassword;
      let count = this.count;
      let errorNum = $("#errorPassworldNum").val();
      // let ifNumAdd1 = false;
      let message = "";
      let same = this.sameFlag;
      let strong = this.password_strength;
      let URL = this.$modifyPwdUrl;
      let baseUrl = this.$baseUrl;
      let username = this.userInfo.username;

      let casLogoutUrl = this.$casLogoutUrl;
      let casLoginUrl = this.$casLoginUrl;
      let webUrl = this.$webUrl;

      //查询用户信息
      $.ajax({
        async: false,
        url: URL + "/user/crossForbidUser?errorPassworld=" + errorNum,
        type: "get",
        dataType: "jsonp", // 返回的数据类型，设置为JSONP方式
        jsonp: "callback", //服务端用于接收callback调用的function名的参数
        success: function(res, status, xhr) {
          if (res.code == 200) {
            var data = res.data;
            if (data) {
              //次数没有超过10
              let origin = res.param;
              // this.checkUserFailed();
              if (!old) {
                // this.passwordErrorMsg = "请输入原密码！";
                // message = "请输入原密码！";
                alert("请输入原密码！");
              } else {
                if (origin != old) {
                  alert(
                    "原密码输入错误！您还剩" + (count - errorNum) + "次机会！"
                  );
                  // message =
                  //   "原密码输入错误！您还剩" + (count - errorNum) + "次机会！";
                  // ifNumAdd1 = true;

                  $("#errorPassworldNum").val(Number(errorNum) + 1);
                  // this.errorPassworldNum = this.errorPassworldNum + 1;
                } else {
                  // errorNum = 0;
                  $("#errorPassworldNum").val(0);
                  message = "";
                  if (strong && same) {
                    //密码强度符合，新密码和旧密码一致
                    // this.modifyUser();
                    $.ajax({
                      async: true,
                      url:
                        URL +
                        "/modify/modifyUser/" +
                        username +
                        "/" +
                        newPwd +
                        "/" +
                        old,
                      type: "GET",
                      dataType: "json", // 返回的数据类型，设置为JSONP方式
                      success: function(response, status, xhr) {
                        if (status) {
                          if (response.code == 200) {
                            // this.successMsg("修改成功,请重新登录");
                            // this.loginOutUrl();
                            alert("修改成功,请重新登录");

                            $.ajax({
                              async: true,
                              url: baseUrl + "/user/loginOut",
                              type: "POST",
                              success: function(res, status, xhr) {
                                let url =
                                  casLogoutUrl +
                                  "?service=" +
                                  casLoginUrl +
                                  "?service=" +
                                  webUrl;
                                //// 前后端一起打包的退出
                                // if (
                                //   status == "success" &&
                                //   res.indexOf("cas/login") > 0
                                // ) {
                                location.href = url;
                                return;
                                // }else{
                                //   alert("请求异常,请手动退出,重新登录");
                                // }
                                //// 前后端分离的退出
                                // if (res.code == 200) {
                                //   if (res.datas) {
                                //     if (casLogoutUrl && casLoginUrl && webUrl) {
                                //       //前端都获取的到,就用前端的
                                //       window.location.replace(url);
                                //     } else {
                                //       //否则用后端返回的地址
                                //       window.location.replace(res.datas);
                                //     }
                                //   } else {
                                //     window.location.replace(url);
                                //   }
                                // }
                              },
                              error: function() {
                                alert("请求异常,请手动退出,重新登录");
                              }
                            });
                          } else {
                            alert(response.msg);
                            // this.passwordErrorMsg = response.msg;
                          }
                        }
                      }
                    });
                  } else {
                    if (!strong) {
                      // this.passwordErrorMsg = "密码强度需达到中级及以上！";
                      // message = "密码强度需达到中级及以上！";
                      alert("密码强度需达到中级及以上！");
                    } else {
                      if (!same) {
                        // this.passwordErrorMsg = "两次输入的新密码不一致！";
                        // message = "两次输入的新密码不一致！";
                        alert("两次输入的新密码不一致！");
                      }
                    }
                    return false;
                  }
                }
              }
            }
          } else if (res.code == 201) {
            // this.passwordErrorMsg = "密码校验次数超限！请联系管理员解禁！";
            alert("密码校验次数超限！请联系管理员解禁！");
          } else if (res.code == 501) {
            //用户已经被删除了
            alert("用户不存在");
            // this.warningMsg("用户不存在");
            this.loginOutUrl();
          } else if (res.code == 502) {
            //用户被禁用了
            // this.passwordErrorMsg = "密码校验次数超限！请联系管理员解禁！";
            alert("密码校验次数超限！请联系管理员解禁！");
          } else {
            alert("res.msg");
            // this.passwordErrorMsg = res.msg;
          }
        }
      });

      // if (ifNumAdd1) {
      //   this.errorPassworldNum = this.errorPassworldNum + 1;
      // }
      // this
    },
    checkUserFailed() {},
    goHome() {
      this.$router.push({ path: "/" });
    },
    showUserDetail() {
      $(".user_floatShow")
        .on("mouseover", function() {
          $(".user_float").removeClass("hide");
        })
        .on("mouseout", function() {
          $(".user_float").addClass("hide");
        });
      // 用户名浮层--鼠标经过浮层浮层显示，离开隐藏
      $(".user_float")
        .on("mouseover", function() {
          $(this).removeClass("hide");
        })
        .on("mouseout", function() {
          $(this).addClass("hide");
        });
    },
    flushRoute(){
      let  n  = this.$route.path;
    if(n =='/'){
      this.routeIndex = "1";
      return;
    }
    if(n =='/fileManage'){
      this.routeIndex = "2";
      return;
    }
    if(n =='/ZKInfo'){
      this.routeIndex = "3";
      return;
    }
    if(n =='/systemMonitor'){
      this.routeIndex = "4";
      return;
    }
    }
    //
  }
  // props:['menumessage']
};
</script>



